import axios from '@/axios'
import Api from '../../constants/Api'

export default {
  namespaced: true,
  state: {
    hostList: {
      data: [],
      total: 0
    },
    userInfo: {
      list: [],
      total: 0
    }
  },
  mutations: {},
  actions: {
    getHostList({state}, data = {}) {
      axios({
        method: 'post',
        url: Api.hostList,
        data
      }).then(res => {
        state.hostList.data = res.data.data
        state.hostList.total = res.data.recordsTotal
      }).catch((err) => {
        console.log(err)
      })
    },
    modifyHost({state}, data = []) {
      axios({
        method: 'post',
        url: Api.hostModify,
        params: {
          oldIp: data[0]
        },
        data: data[1]
      }).then(res => {
        data[2]()
      })
    },
    addHost({state}, data) {
      return axios({
        method: 'post',
        url: Api.hostAdd,
        data
      })
    },
    deleteHostAxios({state}, data) {
      return axios({
        method: 'post',
        url: Api.hostDelete,
        data
      })
    },
    getUserList({state}, data) {
      axios({
        method: 'post',
        url: Api.hostUserList,
        data
      }).then(res => {
        state.userInfo = {
          list: res.data.data,
          total: res.data.recordsTotal
        }
      })
    },
    addHostUser({state}, data) {
      return axios({
        method: 'post',
        url: Api.hostUserAdd,
        data
      })
    },
    updateHostUser({state}, data) {
      return axios({
        method: 'post',
        url: Api.hostUserUpdate,
        params: data[0],
        data: data[1]
      })
    },
    delteHostUser({state}, data) {
      return axios({
        method: 'post',
        url: Api.hostUserDelete,
        data
      })
    }
  }
}



