<template>
  <header class="header clearfix">
    <div class="menu-collapse pull-left">
      <i :class="!isCollapse ? 'fa-dedent' : 'fa-indent'" class="fa" @click="collapse"></i>
      <span class="subline"></span>
      <span class="logo-text">Aries 运维监控平台</span>
    </div>
    <div class="login-user pull-right">
      <el-dropdown trigger="click" placement="bottom">
      <span class="el-dropdown-link">
        {{username}}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="settings">设置</el-dropdown-item>
          <el-dropdown-item @click.native="loginOut">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <personal-center v-if="centerVisible"></personal-center>
  </header>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data() {
    return {
      username: localStorage.getItem('userName'),
      centerVisible: false
    }
  },
  computed: {
    ...mapState({
      isCollapse: state => state.app.isCollapse
    })
  },
  methods: {
    ...mapMutations(['collapse']),
    loginOut() {
      localStorage.removeItem('token')
      localStorage.removeItem('userName')
      sessionStorage.removeItem('userInfo')
      this.$router.push('/login')
    },
    settings() {
      this.centerVisible = true
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../assets/common.scss";

.header {
  padding: 0 20px 0 10px;
  background: #fff;
  @include line-height(60px);
  border-bottom: 1px solid #f2f2f2;
  .menu-collapse {
    @include line-height(60px);
  }
  .login-user {
    padding-top: 24px;
    line-height: normal;
    color: $gray;
    font-size: $font14;
  }
  .fa {
    cursor: pointer;
    color: $dark-gray;
    font-size: 20px;
    vertical-align: middle;
  }
  .el-dropdown {
    display: block;
  }
  .el-dropdown-link {
    color: $gray;
    cursor: pointer;
  }
  .subline {
    vertical-align: middle;
    display: inline-block;
    height: 16px;
    margin-left: 20px;
    border-left: 1px solid $dark-gray;
  }
  .logo-text {
    vertical-align: middle;
    font-size: 22px;
    margin-left: 20px;
    color: $dark-gray;
  }
}
</style>



