import Api from '../../constants/Api'

export default {
  namespaced: true,
  state: {
    project: {
      list: [],
      total: 0
    },
    isCompletion: false,
    envList: [],
    clusters: [],
    namespaceData: [],
    namespaceOptions: [], // 不包含application的命名空间数组
    changeHistory: {
      list: [],
      totalPage: 0
    },
    grayData: [
      {
        isDeleted: false,
        isModified: true,
        newValue: 'gray',
        oldValue: '',
        masterReleaseValue: '',
        item: {
          id: 16,
          key: 'gray',
          value: 'gray',
          dataChangeLastModifiedBy: 'dcits',
          dataChangeLastModifiedTime: '2018-08-29 10:13:46'
        }
      }
    ],
    rollBackActive: '',
    rollBackCompare: [],
    rollbackConfirm: [],
    instanceData: {
      list: [],
      total: 0
    },
    ruleInstanceData: [],
    publicNamespace: []
  },
  mutations: {
    changeNamepsace(state, data) {
      state.namespaceData[data.index].secondEntity.grayRuleData = data.data
    },
    setNamesapceOption(state, data) {
      state.namespaceOptions = data.filter(item => item.firstEntity.baseInfo.namespaceName !== 'application')
    },
    dealNameSpace(state, data) {
      if (data.secondEntity) {
        data.secondEntity.masterItems = []
        data.secondEntity.branchItems = []
        let masterItemsMap = {}
        data.firstEntity.items.forEach(function (item) {
          if (item.item.key) {
            masterItemsMap[item.item.key] = item
          }
        })
        let branchItemsMap = {}
        let itemModifiedCnt = 0
        data.secondEntity.items.forEach(function (item) {
          let key = item.item.key
          let masterItem = masterItemsMap[key]
          // modify master item and set item's masterReleaseValue
          if (masterItem) {
            if (masterItem.isModified && masterItem.oldValue) {
              item.masterReleaseValue = masterItem.oldValue
            } else if (masterItem.item.value) {
              item.masterReleaseValue = masterItem.item.value
            }
          } else {
            // delete branch item
            item.masterReleaseValue = ''
          }
          // delete master item. ignore
          if (item.isDeleted && masterItem) {
            if (item.masterReleaseValue !== item.oldValue) {
              itemModifiedCnt++
              data.secondEntity.branchItems.push(item)
            }
          } else {
            // branch's item
            branchItemsMap[key] = item
            if (item.isModified) {
              itemModifiedCnt++
            }
            data.secondEntity.branchItems.push(item)
          }
        })
        data.secondEntity.itemModifiedCnt = itemModifiedCnt
        data.firstEntity.items.forEach(function (item) {
          if (item.item.key) {
            if (!branchItemsMap[item.item.key]) {
              data.secondEntity.masterItems.push(item)
            } else {
              item.hasBranchValue = true
            }
          }
        })
        console.log(data.secondEntity.branchItems)
      }
    }
  },
  actions: {
    getProjectList({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.projectApps,
        params: {
          page: that.currentPage,
          size: that.pageSize
        }
      }).then(res => {
        state.project = {
          list: res.data,
          total: res.totalElements
        }
      })
    },
    delProject({state}, that) {
      that.$axios({
        method: 'delete',
        url: `/apollo-portal/apps/${that.appId}`
      }).then(res => {
        that.$message({
          type: 'success',
          message: '删除成功'
        })
        that.currentPage = 1
        that.getProjectList(that)
      }).catch(err => {
        console.log(err)
        that.$message({
          type: 'success',
          message: '删除失败'
        })
      })
    },
    deleteCluster({state}, that) {
      that.$axios({
        method: 'delete',
        url: `/apollo-portal/apps/${that.appId}/envs/${that.currentEnv}/clusters/${that.clusterName}`
      }).then(res => {
        that.$message({
          type: 'success',
          message: '删除成功'
        })
        that.getEnvClusterList(that)
      }).catch(err => {
        console.error(err)
        that.$message({
          type: 'success',
          message: '删除失败'
        })
      })
    },
    createProject({state}, that) {
      const params = that.projectForm
      return that.$axios({
        method: 'post',
        url: Api.createApps,
        data: params
      }).then(res => res)
    },
    createNewCluster({state}, that) {
      const params = that.clusterForm
      const data = {
        appId: params.appId,
        name: params.clusterName
      }
      const promises = params.checkEnvList.map(dev => {
        return that.$axios({
          method: 'post',
          url: `/apollo-portal/apps/${params.appId}/envs/${dev}/clusters`,
          data
        })
      })
      Promise.all(promises).then(res => {
        that.clusterVisible = false
        that.$message({
          type: 'success',
          message: '创建集群成功'
        })
        that.getEnvClusterList(that)
      }).catch(err => {
        that.$message({
          type: 'error',
          message: (err && err.data && err.data.message) || '创建集群失败'
        })
      })
    },
    getEnvClusterList({state}, that) {
      let url = Api.envClusterList.replace('{appId}', that.appId)
      state.clusters = []
      return that.$axios({
        method: 'get',
        url: url
      }).then(res => {
        res.data.forEach(env => {
          env.clusters.forEach(item => {
            state.clusters.push({
              env: env.env,
              cluster: item.name
            })
          })
        })
        state.envList = res.data
        return res
      })
    },
    // 获取公共namespace
    getPublicNamespace({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.publicNamespace,
        params: {
          appId: that.appId
        }
      }).then(res => {
        state.publicNamespace = res.data
      })
    },
    // 关联公共namespace
    postPublicNamespace({state}, that) {
      const params = that.namespaceForm
      const data = []
      params.checkedCluster.forEach(item => {
        data.push({
          env: item.split('-')[0],
          namespace: {
            appId: that.appId,
            clusterName: item.split('-')[1],
            namespaceName: params.namespacePublic
          }
        })
      })
      that.$axios({
        method: 'post',
        url: `/apollo-portal/apps/${that.appId}/namespaces`,
        data
      }).then(res => {
        that.$message({
          type: 'success',
          message: '关联成功'
        })
        that.cancelNamespace()
        that.getNamespaceData(that)
      })
    },
    // 创建namespace
    createNamespace({state}, that) {
      const params = that.createNamespaceForm
      const data = {
        appId: that.appId,
        comment: params.comment,
        format: 'properties',
        isPublic: params.type === 1,
        name: params.name
      }
      that.$axios({
        method: 'post',
        url: `/apollo-portal/apps/${that.appId}/appnamespaces`,
        data
      }).then(res => {
        console.log(res)
        that.$message({
          type: 'success',
          message: '创建成功'
        })
        that.cancelNamespace()
        that.getNamespaceData(that)
      })
    },
    getNamespaceData({state, commit}, that) {
      let url = Api.namespaces.replace('{appId}', that.appId)
      url = url.replace('{env}', that.currentEnv)
      url = url.replace('{clusterName}', that.currentCluster)
      // 获取关联的公共namespace方法
      const requestLink = function(namespaceName) {
        return that.$axios({
          method: 'get',
          url: `/apollo-portal/envs/${that.currentEnv}/apps/${that.appId}/clusters/${that.currentCluster}/namespaces/${namespaceName}/associated-public-namespace`
        })
      }
      that.$axios({
        type: 'get',
        url
      }).then(res => {
        commit('setNamesapceOption', that.commonFunc.arrCopy(res.data))
        res.data.forEach(item => {
          item.firstEntity.isLinkedNamespace = item.firstEntity.isPublic ? item.firstEntity.parentAppId !== item.firstEntity.baseInfo.appId : false
          item.firstEntity.branchName = 'master'
          item.firstEntity.isCollapse = true
          item.firstEntity.currentView = 'table'
          item.firstEntity.instanceName = 'new'
          item.firstEntity.instanceData = {
            list: [],
            total: 0,
            current: 1
          }
          item.firstEntity.changeHistory = {
            list: [],
            totalPage: 1,
            current: 1
          }
          item.firstEntity.latestConfigInfo = {}
          if (that.operaitonNamespace === item.firstEntity.baseInfo.namespaceName) {
            item.firstEntity.isCollapse = true
          }
          if (that.recordGrayName === item.firstEntity.baseInfo.namespaceName) {
            item.firstEntity.branchName = 'gray'
          }
          if (item.secondEntity) {
            item.secondEntity.currentGrayView = 'config'
            item.secondEntity.grayRuleData = []
            item.secondEntity.grayInstanceData = {
              list: [],
              total: 0,
              current: 1
            }
            item.secondEntity.changeHistory = {
              list: [],
              totalPage: 1,
              current: 1
            }
            item.secondEntity.latestGrayConfigInfo = {}
          }
          commit('dealNameSpace', item)
        })
        const resData = res.data
        const linkPromises = []
        resData.forEach(item => {
          if (item.firstEntity.isLinkedNamespace) {
            linkPromises.push(requestLink(item.firstEntity.baseInfo.namespaceName))
            // item.firstEntity.linkedConfigs = linkRes.data
          }
        })
        Promise.all(linkPromises).then(resAll => {
          let resIndex = 0
          resData.forEach((link, index) => {
            if (link.firstEntity.isLinkedNamespace) {
              link.firstEntity.linkedConfigs = resAll[resIndex].data
              resIndex += 1
            }
          })
          state.namespaceData = resData
        })
      })
    },
    // 配置灰度
    configGrayPost({state}, that) {
      const replaceUrl = (envCluster) => {
        let url = Api.addConfig.replace('{appId}', that.configForm.appId)
        url = url.replace('{env}', envCluster.split('-')[0])
        url = url.replace('{clusterName}', that.configForm.grayName)
        url = url.replace('{namespaceName}', that.configForm.namespaceName)
        return url
      }
      const putParams = Object.assign({tableViewOperType: 'create'}, that.configForm)
      that.$axios({
        method: 'post',
        url: replaceUrl(that.currentCluster),
        data: putParams
      }).then(res => {
        that.$message({
          type: 'success',
          message: '修改成功'
        })
        that.$emit('cancelConfig')
      })
    },
    // 新增、修改配置
    async addConfiguration({state}, that) {
      const params = that.paramsForm
      const clusterArr = that.configForm.checkedCluster
      const replaceUrl = (envCluster) => {
        let url = Api.addConfig.replace('{appId}', that.configForm.appId)
        url = url.replace('{env}', envCluster.split('-')[0])
        if (that.configForm.grayName) {
          url = url.replace('{clusterName}', that.configForm.grayName)
        } else {
          url = url.replace('{clusterName}', envCluster.split('-')[1])
        }
        url = url.replace('{namespaceName}', that.configForm.namespaceName)
        return url
      }
      if (clusterArr && clusterArr.length > 0) {
        const ajaxAll = []
        clusterArr.forEach(item => {
          ajaxAll.push(
            that.$axios({
              method: 'post',
              url: replaceUrl(item),
              data: params
            }))
        })
        Promise.all(ajaxAll)
        .then(res => {
          that.$message({
            type: 'success',
            message: '新增成功'
          })
          that.$emit('cancelConfig', that.configForm.grayName)
        })
        .catch(err => {
          console.log(err)
          that.$message({
            type: 'error',
            message: err.data.message
          })
        })
      } else {
        const putParams = Object.assign({tableViewOperType: 'update'}, that.configForm)
        that.$axios({
          method: 'put',
          url: replaceUrl(that.currentCluster),
          data: putParams
        }).then(res => {
          that.$message({
            type: 'success',
            message: '修改成功'
          })
          that.$emit('cancelConfig', that.configForm.grayName)
        })
      }
    },
    // 删除配置
    deleteConfig({state}, that) {
      const params = that.configForm
      let url = `/apollo-portal/apps/${params.appId}/envs/${that.currentEnv}/clusters/{cluster}/namespaces/${params.namespaceName}/items/${params.id}`
      if (params.grayName) {
        url = url.replace('{cluster}', params.grayName)
      } else {
        url = url.replace('{cluster}', that.currentCluster)
      }
      that.$axios({
        method: 'delete',
        url
      }).then(res => {
        that.$message({
          type: 'success',
          message: '删除成功'
        })
        that.getNamespaceData(that)
      })
    },
    addGrayRuleData({state}, that) {
      const data = that.ruleParams
      const params = {
        appId: data.appId,
        branchName: data.branchName,
        clusterName: data.clusterName,
        namespaceName: data.namespaceName,
        ruleItems: [
          {
            ApplyToAllInstances: false,
            clientAppId: data.appId,
            clientIpList: that.paramsIpList,
            draftIpList: that.paramsIpList,
            isNew: data.isNew
          }
        ]
      }
      that.$axios({
        method: 'put',
        url: `/apollo-portal/apps/${data.appId}/envs/${data.currentEnv}/clusters/${data.clusterName}/namespaces/${data.namespaceName}/branches/${data.branchName}/rules`,
        data: params
      }).then(res => {
        let message = '新增成功'
        if (!data.isNew) message = '修改成功'
        that.$message({
          type: 'success',
          message
        })
        that.onCancel()
        that.$parent.getRuleData(data)
      })
    },
    deleteGrayRuleData({state}, that) {
      const data = that.ruleParams
      data.ruleItems = []
      that.$axios({
        method: 'put',
        url: `/apollo-portal/apps/${data.appId}/envs/${data.currentEnv}/clusters/${data.clusterName}/namespaces/${data.namespaceName}/branches/${data.branchName}/rules`,
        data
      }).then(res => {
        that.$message({
          type: 'success',
          message: '删除成功'
        })
        that.getRuleData(data)
      })
    },
    // 获取新增规则弹窗实例列表
    getRuleInstance({state}, that) {
      const data = that.ruleParams
      const params = {
        appId: data.appId,
        clusterName: data.clusterName,
        instanceAppId: data.appId,
        namespaceName: data.namespaceName,
        page: 0,
        size: 2000
      }
      that.$axios({
        method: 'get',
        url: Api.addRuleInstance.replace('{env}', that.currentEnv),
        params
      }).then(res => {
        state.ruleInstanceData = res.content
      })
    },
    getGrayRuleData({state}, that) {
      let url = `/apollo-portal/apps/${that.appId}/envs/${that.currentEnv}/clusters/${that.currentCluster}/namespaces/${that.namespaceName}/branches/${that.branchName}/rules`
      return that.$axios({
        method: 'get',
        url
      }).then(res => res)
    },
    modifyText({state}, that) {
      const params = {...that.textTypeForm}
      delete params.namespaceName
      return that.$axios({
        method: 'put',
        url: `/apollo-portal/apps/${that.appId}/envs/${that.currentEnv}/clusters/${that.currentCluster}/namespaces/${that.textTypeForm.namespaceName}/items`,
        data: params
      }).then(res => {
        that.$message({
          type: 'success',
          message: '更新成功，如需生效请发布'
        })
        that.isReadOnly = true
      })
    },
    // 获取更改历史
    getNamespaceHistory({state}, that) {
      let clusterName = that.currentCluster
      if (that.currentVersion !== 'master' && that.currentViews === 'grayHistory') {
        clusterName = that.grayBranchName
      }
      return that.$axios({
        method: 'get',
        url: `/apollo-portal/apps/${that.appId}/envs/${that.currentEnv}/clusters/${clusterName}/namespaces/${that.namespaceName}/commits_page`,
        params: that.historyPage
      }).then(res => {
        res.data && res.data.forEach(item => {
          item.changeSets = JSON.parse(item.changeSets)
        })
        return res
      })
    },
    getMissingNamespace({state}, that) {
      let url = Api.configMissing.replace('{appId}', that.appId)
      url = url.replace('{env}', that.currentEnv)
      url = url.replace('{clusterName}', that.currentCluster)
      that.$axios({
        method: 'get',
        url
      }).then(res => {
        if (res.data && res.data.length > 0) {
          state.isCompletion = true
          that.$notify({
            title: '命名空间缺失',
            type: 'error',
            message: '当前环境有命名空间缺失，请点击页面顶部“补缺命名空间”按钮来补齐数据'
          })
        } else {
          state.isCompletion = false
        }
      })
    },
    postMissingNamespace({state}, that) {
      let url = Api.configMissing.replace('{appId}', that.appId)
      url = url.replace('{env}', that.currentEnv)
      url = url.replace('{clusterName}', that.currentCluster)
      that.$axios({
        method: 'post',
        url
      }).then(res => {
        that.$message.success('补缺成功')
        that.getNamespaceData(that)
        that.getMissingNamespace(that)
      }).catch(err => {
        that.$message.error((err && err.data && err.data.message) || '补缺失败')
      })
    },
    // 获取实例最新配置信息
    getNewConfigInfo({state}, that) {
      let clusterName = that.currentCluster
      if (that.currentViews === 'grayInstance') {
        clusterName = that.grayBranchName
      }
      return that.$axios({
        method: 'get',
        url: `/apollo-portal/apps/${that.appId}/envs/${that.currentEnv}/clusters/${clusterName}/namespaces/${that.namespaceName}/releases/active`,
        params: {
          page: 0,
          size: 1
        }
      }).then(res => res)
    },
    // 获取实例列表
    getInstanceData({state}, that) {
      let url = Api.instanceList.replace('{env}', that.currentEnv)
      let params = {
        appId: that.appId,
        clusterName: that.currentCluster,
        namespaceName: that.namespaceName,
        flag: that.currentInstance,
        page: that.instanceCurrentPage,
        size: 10
      }
      if (that.currentInstance !== 'all' && that.latestConfigInfo) {
        params.releaseId = that.latestConfigInfo.id
      }
      if (that.currentViews === 'grayInstance' && that.latestGrayConfigInfo) {
        url = Api.grayInstanceList.replace('{env}', that.currentEnv)
        params = {
          releaseId: that.latestGrayConfigInfo.id,
          page: that.grayInstanceCurrentPage,
          size: 10
        }
      }
      return that.$axios({
        method: 'get',
        url,
        params
      }).then(res => res)
    },
    release({state}, that) {
      const params = Object.assign({
        IsEmergencyPublish: false
      }, that.releaseForm)
      return that.$axios({
        method: 'post',
        url: `/apollo-portal/apps/${that.appId}/envs/${that.currentEnv}/clusters/${that.currentCluster}/namespaces/${that.namespaceName}/releases`,
        data: params
      }).then(res => res)
    },
    getRollBackData({state}, that) {
      const url = '/apollo-portal/apps/' + that.rollBackPara.appId + '/envs/' + that.rollBackPara.env + '/clusters/' + that.rollBackPara.clusterName + '/namespaces/' + that.rollBackPara.namespaceName + '/releases/active'
      const params = {
        page: 0,
        size: 2
      }
      that.$axios({
        method: 'get',
        url: url,
        params: params
      }).then(res => {
        state.rollBackActive = res.data[0].name + ' 回滚到 ' + res.data[1].name
        that.$nextTick(() => {
          const obj = {
            baseReleaseId: res.data[0].id,
            toCompareReleaseId: res.data[1].id
          }
          state.rollbackConfirm = {
            env: that.rollBackPara.env,
            releaseId: res.data[0].id
          }
          that.handleBackCompared(obj)
        })
      })
    },
    // 对比两次配置版本
    getBackCompared({state}, that) {
      const params = that.comparePara
      const url = '/apollo-portal/envs/' + that.currentEnv + '/releases/compare'
      that.$axios({
        method: 'get',
        url: url,
        params: params
      }).then(res => {
        state.rollBackCompare = res.data.changes
      }).catch(err => {
        console.log(err)
      })
    },
    setRollBackData({state}, that) {
      const url = '/apollo-portal/envs/' + state.rollbackConfirm.env + '/releases/' + state.rollbackConfirm.releaseId + '/rollback'
      that.$axios({
        method: 'put',
        url: url
      }).then(() => {
        that.$router.push({
          name: 'configDetail',
          query: {
            appId: that.rollBackPara.appId
          }
        })
        that.$message.success('回滚成功')
        that.$parent.getNamespaceData(that.$parent)
      }).catch(err => {
        that.$message.error('回滚失败')
        console.log(err)
      })
    },
    deleteNameSpace({state}, that) {
      const url = '/apollo-portal/apps/' + that.appId + '/appnamespaces/' + that.deleteNamespaceForm.selectNamespace
      that.$axios({
        method: 'delete',
        url: url
      }).then(() => {
        that.getNamespaceData(that)
        that.delSpaceVisible = false
        that.$message.success('NameSpace删除成功')
      })
    },
    addGrayListData({state}, that) {
      const url = '/apollo-portal/apps/' + that.addGrayParam.appId + '/envs/' + that.addGrayParam.env + '/clusters/' + that.addGrayParam.clusterName + '/namespaces/' + that.addGrayParam.namespaceName + '/branches'
      that.$axios({
        method: 'post',
        url: url
      }).then(() => {
        that.addGrayVisible = false
        that.$message.success('灰度添加成功')
        that.getNamespaceData(that)
      }).catch(err => {
        that.$message.error('灰度添加失败')
        console.log(err)
      })
    },
    delGrayListData({state}, that) {
      const url = '/apollo-portal/apps/' + that.delGrayParam.appId + '/envs/' + that.delGrayParam.env + '/clusters/' + that.delGrayParam.clusterName + '/namespaces/' + that.delGrayParam.namespaceName + '/branches/' + that.delGrayParam.branchName
      that.$axios({
        method: 'delete',
        url: url
      }).then(() => {
        that.delGrayVisible = false
        that.$message.success('灰度删除成功')
        that.getNamespaceData(that)
      }).catch(err => {
        that.$message.error('灰度删除失败')
        console.log(err)
      })
    },
    branchPublish({state}, that) {
      const para = that.$parent.grayReleasePara
      const url = '/apollo-portal/apps/' + para.appId + '/envs/' + para.env + '/clusters/' + para.clusterName + '/namespaces/' + para.namespaceName + '/branches/' + para.branchName + '/releases'
      that.$axios({
        method: 'post',
        url: url,
        data: that.grayReleaseForm
      }).then(res => {
        that.$message.success('灰度发布成功')
        that.$parent.getNamespaceData(that.$parent)
        console.log(res)
      }).catch(err => {
        that.$message.error('灰度删除失败')
        console.log(err)
      })
    },
    mergeAndPublish({state}, that) {
      const para = that.$parent.grayReleasePara
      const url = '/apollo-portal/apps/' + para.appId + '/envs/' + para.env + '/clusters/' + para.clusterName + '/namespaces/' + para.namespaceName + '/branches/' + para.branchName + '/merge'
      that.$axios({
        method: 'post',
        url: url,
        params: {
          deleteBranch: that.deleteBranch
        },
        data: that.grayReleaseForm
      }).then(res => {
        that.$message.success('全量发布成功')
        that.$parent.getNamespaceData(that.$parent)
        console.log(res)
      }).catch(err => {
        that.$message.error((err && err.data && err.data.message) || '全量删除失败')
        console.log(err)
      })
    },
    // 获取实例number
    getInstanceNumber({state}, that) {
      let url = Api.instanceNumber.replace('{appId}', that.appId)
      url = url.replace('{env}', that.currentEnv)
      url = url.replace('{clusterName}', that.currentCluster)
      url = url.replace('{namespaceName}', that.namespaceName)
      return that.$axios({
        method: 'get',
        url
      })
    }
  }
}



