<template>
  <div class="tab-router clearfix">
    <ul class="tab-container" :style="{float:myPs}">
      <router-link class="my-list"
                   :class="{'active': index === activeId}"
                   tag="li"
                   v-for="(item,index) of tabList"
                   :key="index"
                   :to="item.url"
                   :title="item.label"
                   @click.native="handelClick(index)">
        {{item.label}}
      </router-link>
    </ul>
    <hr class="hr-style" :style="{float:myPs,height:htHeight}"/>
    <div class="dc-tab-active" :style="[tabTransform,marginStyle]"></div>
    <div class="main-container" :style="mainPadding">
      <keep-alive>
        <router-view/>
      </keep-alive>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'DcRouterTab',
    props: ['tabPosition', 'tabList'],
    data() {
      return {
        activeId: 0,
        mainPadding: {},
        marginStyle: {},
        tabTransform: {
          transform: 'translateY(0' + 'px)',
          height: 40 + 'px',
          float: 'right'
        }
      }
    },
    computed: {
      myPs() {
        var that = this
        if (this.tabPosition && this.tabPosition !== '') {
          if (this.tabPosition === 'left') {
            that.mainPadding = {
              paddingLeft: 20 + 'px'
            }
            that.marginStyle = {
              'margin-left': -2 + 'px'
            }
          } else {
            that.mainPadding = {
              paddingRight: 20 + 'px'
            }
            that.marginStyle = {
              'margin-right': -2 + 'px'
            }
          }
          that.tabTransform.float = this.tabPosition
          return this.tabPosition
        } else {
          that.mainPadding = {
            paddingLeft: 20 + 'px'
          }
          that.marginStyle = {
            'margin-left': -2 + 'px'
          }
          that.tabTransform.float = 'left'
          return 'left'
        }
      },
      htHeight() {
        return (this.tabList.length) * 40 + 20 + 'px'
      }
    },
    methods: {
      handelClick(index) {
        this.activeId = index
        this.tabTransform.transform = 'translateY(' + this.activeId * 40 + 'px)'
      },
      findIndex() {
        var _list = this.tabList
        var index = 0
        for (let i = 0; i < _list.length; i++) {
          if (this.$route.meta.title === _list[i].label) {
            index = i
            break
          }
        }
        return index
      }
    },
    mounted() {
      this.activeId = this.findIndex()
    },
    updated() {
      this.activeId = this.findIndex()
      this.tabTransform.transform = 'translateY(' + this.activeId * 40 + 'px)'
    }
  }
</script>
<style scoped>
  .tab-router {
    overflow: hidden;
  }

  .tab-container {
    position: relative;
    width: 90px;
    text-align: left;
  }

  .main-container {
    position: relative;
    overflow: hidden;
  }

  .my-list {
    position: relative;
    padding: 0;
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    line-height: 40px;
    list-style: none;
    font-size: 14px;
    font-weight: 500;
    color: #303133;
    overflow: hidden;
    white-space: nowrap;
    text-align: center;
    text-overflow: ellipsis;
    cursor: pointer;
  }

  .my-list:hover {
    color: #409eff;
  }

  .hr-style {
    width: 2px;
    border: 1px solid #e4e7ed;
  }

  .active {
    color: #409eff;
  }

  .dc-tab-active {
    left: 0;
    top: 0;
    bottom: auto;
    width: 2px;
    height: auto;
    background-color: #409eff;
    z-index: 1;
    transition: transform .3s cubic-bezier(0.65, 0.05, 0.36, 1);
    list-style: none;
  }
</style>



