import Api from '../../constants/Api'

export default {
  namespaced: true,
  state: {
    deptDataList: [],
    menuDataList: [],
    roleMenuTree: [],
    roleDataList: [],
    deptTree: [],
    userDataList: []
  },
  mutations: {
    detailDeptDataTo(state, data) {
      state.deptDataList = data
    },
    detailMenuDataTo(state, data) {
      state.menuDataList = data
    },
    detailRoleDataTo(state, data) {
      state.roleDataList = data
    },
    roleMenuTreeTo(state, data) {
      state.roleMenuTree = data
    },
    detailUserDataTo(state, data) {
      state.userDataList = data
    },
    deptTreeTo(state, data) {
      state.deptTree = data
    }
  },
  actions: {
    getDeptList({ commit }, that) {
      that.$axios({
        method: 'get',
        url: Api.getDeptList
      }).then(res => {
        commit('detailDeptDataTo', res.data)
        that.tableLoading = false
      })
    },
    addNewDept({ commit }, that) {
      let _data = {
        parent_id: parseInt(that.deptForm.parent_id),
        name: that.deptForm.name,
        order_num: parseInt(that.deptForm.order_num),
        del_flag: parseInt(that.deptForm.del_flag)
      }
      that.$axios({
        method: 'post',
        url: Api.addNewDept,
        data: _data
      }).then(res => {
        that.$message.success(res.message)
        that.getData()
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    editDept({ state }, that) {
      let _data = {
        dept_id: parseInt(that.deptForm.dept_id),
        parent_id: parseInt(that.deptForm.parent_id),
        name: that.deptForm.name,
        order_num: parseInt(that.deptForm.order_num),
        del_flag: parseInt(that.deptForm.del_flag)
      }
      that.$axios({
        method: 'post',
        url: Api.editDept,
        data: _data
      }).then(res => {
        that.$message.success(res.message)
        that.getData()
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    removeDept({ state }, that) {
      let url = Api.removeDept.replace('{deptId}', that.deptForm.dept_id)
      that.$axios({
        method: 'delete',
        url: url
      }).then(res => {
        that.$message.success(res.message)
        that.getData()
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    deptListById({ state }, that) {
      let url = Api.deptListById.replace('{deptId}', that.deptForm.deptById)
      return that.$axios({
        method: 'get',
        url: url
      }).then(res => {
        return res.data.name
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    getMenuList({ commit }, that) {
      that.$axios({
        method: 'get',
        url: Api.getMenuList
      }).then(res => {
        commit('detailMenuDataTo', res.data)
        that.tableLoading = false
      })
    },
    addSysMenu({ commit }, that) {
      let _data = {
        parent_id: parseInt(that.sysMenuForm.parent_id),
        name: that.sysMenuForm.name,
        url: that.sysMenuForm.url,
        perms: that.sysMenuForm.perms,
        type: that.sysMenuForm.type === '' ? '' : parseInt(that.sysMenuForm.type),
        order_num: that.sysMenuForm.order_num === '' ? '' : parseInt(that.sysMenuForm.order_num),
        icon: that.iconName
      }
      that.$axios({
        method: 'post',
        url: Api.addSysMenu,
        data: _data
      }).then(res => {
        that.$message.success(res.message)
        that.getData()
        that.getNavTree()
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    editSysMenu({ state }, that) {
      let _data = {
        menu_id: parseInt(that.sysMenuForm.menu_id),
        parent_id: parseInt(that.sysMenuForm.parent_id),
        name: that.sysMenuForm.name,
        url: that.sysMenuForm.url,
        perms: that.sysMenuForm.perms,
        type: that.sysMenuForm.type === '' ? '' : parseInt(that.sysMenuForm.type),
        order_num: that.sysMenuForm.order_num === '' ? '' : parseInt(that.sysMenuForm.order_num),
        icon: that.iconName
      }
      that.$axios({
        method: 'post',
        url: Api.editSysMenu,
        data: _data
      }).then(res => {
        that.$message.success(res.message)
        that.getData()
        that.getNavTree()
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    removeMenu({ state }, that) {
      let url = Api.removeMenu.replace('{menuId}', that.sysMenuForm.menu_id)
      return that.$axios({
        method: 'delete',
        url: url
      }).then(res => {
        that.$message.success(res.message)
        that.getData()
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    sysMenuById({ commit }, that) {
      let url = Api.sysMenuById.replace('{menuId}', that.sysMenuForm.sysMenuId)
      return that.$axios({
        method: 'get',
        url: url
      }).then(res => {
        return res.data.name
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    getRoleList({ commit }, that) {
      that.$axios({
        method: 'get',
        url: Api.getRoleList
      }).then(res => {
        commit('detailRoleDataTo', res.data)
        that.currentPage = res.currentPage
        that.totalCount = res.totalCount
        that.tableLoading = false
      })
    },
    getRoleMenuTree({ commit, state }, that) {
      that.$axios({
        method: 'get',
        url: Api.getRoleTree
      }).then(res => {
        let _res = []
        _res.push(res.data)
        commit('roleMenuTreeTo', _res)
      })
    },
    addRole({ commit }, that) {
      that.$axios({
        method: 'post',
        url: Api.addRole,
        data: that.roleForm
      }).then(res => {
        that.getData()
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    editRole({ commit }, that) {
      that.$axios({
        method: 'post',
        url: Api.editRole,
        data: that.roleForm
      }).then(res => {
        that.getData()
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    removeRole({ commit }, that) {
      let url = Api.removeRole.replace('{roleId}', that.roleForm.role_id)
      that.$axios({
        method: 'delete',
        url: url
      }).then(res => {
        that.getData()
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    getUserList({ commit }, that) {
      that.$axios({
        method: 'get',
        url: Api.getUserList,
        params: that.userPara
      }).then(res => {
        commit('detailUserDataTo', res.data)
        that.currentPage = res.currentPage
        that.totalCount = res.totalCount
        that.tableLoading = false
      })
    },
    getPersonInfo({ commit }, that) {
      that.$axios({
        method: 'get',
        url: Api.getPersonInfo
      }).then(res => {
        that.centerForm = res.data
      })
    },
    getDeptTree({ commit }, that) {
      that.$axios({
        method: 'get',
        url: Api.getDeptTree
      }).then(res => {
        let _res = []
        _res.push(res.data)
        commit('deptTreeTo', _res)
      })
    },
    addUser({ commit }, that) {
      const _data = {
        name: that.userForm.name,
        username: that.userForm.username,
        password: that.userForm.password,
        email: that.userForm.email,
        dept_id: parseInt(that.userForm.dept_id),
        status: parseInt(that.userForm.status),
        role_ids: that.userForm.role_ids
      }
      that.$axios({
        method: 'post',
        url: Api.addUser,
        data: _data
      }).then(res => {
        that.getData()
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    editUser({ state }, that) {
      const _data = {
        userId: parseInt(that.userForm.userId),
        name: that.userForm.name,
        username: that.userForm.username,
        email: that.userForm.email,
        dept_id: parseInt(that.userForm.dept_id),
        status: parseInt(that.userForm.status),
        role_ids: that.userForm.role_ids
      }
      that.$axios({
        method: 'post',
        url: Api.editUser,
        data: _data
      }).then(res => {
        that.getData()
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    removeUserOne({ state }, that) {
      let url = Api.removeUserOne.replace('{userId}', that.userForm.userId)
      that.$axios({
        method: 'delete',
        url: url
      }).then(res => {
        that.getData()
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    resetPwd({ state }, that) {
      that.$axios({
        method: 'post',
        url: Api.resetPwd,
        data: that.resetForm
      }).then(res => {
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    batchRemove({ state }, that) {
      that.$axios({
        method: 'post',
        url: Api.batchRemove,
        data: that.multipleSelection
      }).then(res => {
        that.getData()
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    },
    editPersonal({ state }, that) {
      const _data = {
        name: that.centerForm.name,
        username: that.centerForm.username,
        email: that.centerForm.email,
        mobile: that.centerForm.mobile
      }
      that.$axios({
        method: 'post',
        url: Api.editPersonal,
        data: _data
      }).then(res => {
        sessionStorage.setItem('userInfo', JSON.stringify(res.data))
        that.$message.success(res.message)
      }).catch(error => {
        that.$message.error(error.data.message)
      })
    }
  }
}



