  mutations: {
    // 菜单收缩
    collapse(state) {
      state.isCollapse = !state.isCollapse
    },
    // 设置标签
    setTags(state, route = {}) {
      const isExist = state.tagsList.some(item => {
        return item.path === route.fullPath
      })
      !isExist && state.tagsList.push({
        title: route.params.id ? `${route.meta.title}-${route.params.id}` : route.meta.title,
        path: route.fullPath,
        name: route.name
      })
    },
    // 删除单个标签
    delTags(state, data = {}) {
      let index = data.index
      const delItem = state.tagsList.splice(index, 1)[0]
      const item = state.tagsList[index] ? state.tagsList[index] : state.tagsList[index - 1]
      if (data.callback && typeof data.callback === 'function') {
        data.callback({delItem, item})
      }
    }
  },
  actions: {
    getToken({state}, data) {
      let url = Api.getToken.replace('{0}', data.username)
      url = url.replace('{1}', data.password)
      return axios({
        method: 'post',
        url: url,
        auth: {
          username: 'oms-web',
          password: 'oms-web'
        }
      }).then(res => {
        localStorage.setItem('token', res.token_type + ' ' + res.access_token)
        sessionStorage.setItem('userInfo', JSON.stringify(res.principal))
        return res
      }).catch(err => {
        return err
      })
    },
    getBooks({state}) {
      return axios({
        method: 'get',
        url: Api.books
      }).then(res => console.log(res))
    },
    getNavTree({state}, that) {
      return axios({
        method: 'get',
        url: Api.getNavTree
      }).then(res => {
        state.navTree = res.data
        that.loading = false
      }).catch(err => {
        that.loading = false
        return err
      })
    }
  }
