<template>
  <div class="core-log">
    <el-dialog
      :title="name + ' 日志'"
      :visible.sync="visible"
      width="60%"
      center
      @close="close">
      <div class="log-box">
        <p v-for="(item, index) in data" v-text="item" :key="index"></p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    props: {
        visible: Object,
        name: Object,
        data: Object
    },
    beforeUpdate() {
    },
    methods: {
        close() {
            this.$parent.logVisible = false
        }
    }
  }
</script>



