<template>
  <div class="dcNumShow">
    <el-card shadow="always">
      <div slot="header" class="clearfix">
        <div class="dataTitle">{{numData.title}}</div>
      </div>
      <div class="dataTime">时间 : {{numData.time}}</div>
      <div class="dataNum">监控值 : {{numData.num}}</div>
    </el-card>
  </div>
</template>

<script>
  export default {
    props: ['numData'],
    name: 'DcNumShow'
  }
</script>

<style type="text/scss" lang="scss" scoped>
  .dcNumShow /deep/ .el-card__header, .dcNumShow /deep/ .el-card__body {
    color: #fff;
    background: -webkit-linear-gradient(left, #409EFF, #ec6ead);
    /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(right, #409EFF, #ec6ead);
    /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(right, #409EFF, #ec6ead);
    /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #409EFF, #ec6ead);
    /* 标准的语法（必须放在最后） */
  }

  .dcNumShow {
    margin-top: 60px;
    font-size: 20px;
    text-align: center;
    .dataTitle {
      font-weight: bolder;
    }
    .dataTime {
      padding: 10px;
    }
    .dataNum {
      padding: 10px;
    }
  }
</style>



