// import Api from '../../constants/Api'
export default {
  namespaced: true,
  state: {
    navTree: [],
    configList: [],
    diffData: []
  },
  mutations: {},
  actions: {
    getNavTree({state}, that) {
      const url = '/apollo-portal/apps/' + that.params.appId + '/navtree'
      that.$axios({
        method: 'get',
        url: url
      }).then(res => {
        res.data.forEach(function (value) {
          value.clusters.forEach(function (res) {
            res.namespaceName = that.params.namespaceName
          })
        })
        state.navTree = res.data
        that.$nextTick(() => {
          that.checked()
        })
      }).catch(err => {
        console.log(err)
      })
    },
    getConfigList({state}, that) {
      const params = that.params
      const url = '/apollo-portal/apps/' + params.appId + /envs/ + params.env + /clusters/ + params.clusterName +
        '/namespaces/' + params.namespaceName + '/items'
      that.$axios({
        method: 'get',
        url: url,
        params: {
          orderBy: 'lastModifiedTime'
        }
      }).then(res => {
        state.configList = res.data
      }).catch(err => {
        console.log(err)
      })
    },
    getSynData({dispatch, state}, that) {
      const params = that.synParam
      const url = '/apollo-portal/namespaces/' + that.params.namespaceName + '/diff'
      that.$axios({
        method: 'post',
        url: url,
        data: params
      }).then(res => {
        let cnt = 0
        res.data.forEach(async value => {
          let _diffs = []
          let para = {
            namespace: value.namespace,
            updateItems: value.diffs.updateItems,
            diffs: _diffs
          }
          await that.updateItems(para)
          if (value.diffs.createItems.length > 0) {
            value.diffs.createItems.forEach(function (res) {
              res.type = '新增'
              _diffs.push(res)
            })
          }
          if (value.diffs.deleteItems.length > 0) {
            value.diffs.deleteItems.forEach(function (res) {
              res.type = '删除'
              _diffs.push(res)
            })
          }
          value.diffs = _diffs
          if (value.diffs.length > 0) {
            cnt++
          }
          if (cnt > 0) {
            that.hasDiff = true
          } else {
            that.hasDiff = false
          }
        })
        state.diffData = res.data
      }).catch(err => {
        console.log(err)
      })
    },
    setSynData({state}, that) {
      const params = that.synParamList
      console.log(that.synParamList)
      const url = '/apollo-portal/apps/' + that.params.appId + '/namespaces/' + that.params.namespaceName + '/items'
      that.$axios({
        method: 'put',
        url: url,
        data: params
      }).then(res => {
        state.diffData = res.data
        that.title = '(同步成功)'
        that.step = 3
      }).catch(err => {
        console.log(err)
      })
    }
  }
}



