<template>
  <div ref="scrollMain" class="galaxy-scroll" :style="{maxHeight: options.maxHeight}">
    <slot name="main" />
    <slot name="loading" />
    <slot name="footer" />
  </div>
</template>

<script>
export default {
  props: {
    options: Object
  },
  mounted() {
    this.scrollLoadData()
  },
  methods: {
    scrollLoadData() {
      const scrollMain = this.$refs.scrollMain
      scrollMain.addEventListener('scroll', () => {
        if (scrollMain.scrollTop + scrollMain.offsetHeight >= scrollMain.scrollHeight - 50) {
          this.$emit('scrollLoadData')
        }
      })
    }
  }
}
</script>

<style scoped>
.galaxy-scroll {
  background: #333;
  overflow-y: scroll;
  border-radius: 3px;
  min-height: 100px;
}
.galaxy-scroll pre {
  padding: 20px;
  color: #f0f0f0;
  line-height: 24px;
  font-size: 14px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
</style>



