<template>
  <div class="app-container clearfix">
    <page-nav/>
    <div class="page-content" :class="{'content-collapse': isCollapse}">
      <page-header/>
      <page-tags/>
      <div class="content-box">
        <div class="content">
          <div class="page-main">
            <!-- <breadcrumb /> -->
            <transition name="el-fade-in-linear" mode="out-in">
              <router-view/>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import pageNav from '@/components/part/nav'
import pageHeader from '@/components/part/header'
import pageTags from '@/components/part/tags'
// import breadcrumb from '@/components/part/breadcrumb'

import {mapState} from 'vuex'

export default {
  components: {
    pageNav,
    pageHeader,
    pageTags
    // breadcrumb
  },
  data() {
    return {}
  },
  computed: {
    ...mapState({
      isCollapse: state => state.app.isCollapse
    })
  }
}
</script>
<style></style>



