// 引入接口
import Api from '../../constants/Api'
// 服务的仓库
export default {
  namespaced: true,
  state: {
    // 添加分类
    classify: {
      statusGroup: [
        // 状态分类
        {
          value: '生效',
          label: '生效'
        },
        {
          value: '未生效',
          label: '未生效'
        }
      ],
      demotionGroup: [
        // 降级分组分类
        {
          value: 'force:return+null',
          label: '屏蔽'
        },
        {
          value: 'fail:return+null',
          label: '容错'
        }
      ],
      loadGroup: [
        // 负载分组分类
        {
          value: 'random',
          label: '随机'
        },
        {
          value: 'roundrobin',
          label: '轮询'
        },
        {
          value: 'leastactive',
          label: '最少并发'
        }
      ],
      priorityGroup: [
        // 优先级分类
        {
          value: '0',
          label: '0'
        },
        {
          value: '1',
          label: '1'
        },
        {
          value: '2',
          label: '2'
        },
        {
          value: '3',
          label: '3'
        },
        {
          value: '4',
          label: '4'
        },
        {
          value: '5',
          label: '5'
        },
        {
          value: '6',
          label: '6'
        },
        {
          value: '7',
          label: '7'
        },
        {
          value: '8',
          label: '8'
        },
        {
          value: '9',
          label: '9'
        },
        {
          value: '10',
          label: '10'
        }
      ]
    },
    clusterOption: [], // 集群下拉框
    serviceList: [],
    serviceTotal: 0,
    consumerList: [],
    consumerTotal: 0
  },
  actions: {
    initializeData({state, commit}, {vm}) { // 初始化数据
      return vm.$axios({
        method: 'get',
        url: Api.zkClusterOption
      }).then(res => {
        state.clusterOption = res.data && JSON.parse(res.data)
        return true
      }).catch(() => {
        return false
      })
    },
    // 查询
    serviceFind({
      state,
      commit
    }, data) {
      let vm = data[0]
      let value = data[1]
      let params = Object.assign({}, JSON.parse(JSON.stringify(value)))
      Object.keys(params).forEach(key => {
        if (params[key] === '' || params[key] === undefined || params[key] === null) {
          delete params[key]
        }
      })
      vm.$axios({
        method: 'post',
        url: Api.serviceList,
        data: params
      }).then(res => {
        // 渲染state.serviceList的数据，更新表格
        state.serviceTotal = res.data ? res.data.length : 0
        res.data.forEach((single) => {
          single.zkClusterId = value.zkClusterId
        })
        const stateData = {
          data: res.data,
          name: 'serviceList'
        }
        commit('paginationChange', stateData)
        vm.loading = false
        vm.$message({
          message: '查询成功',
          type: 'success'
        })
      }).catch(error => {
        vm.loading = false
        state.serviceList = []
        vm.currentPage = 1
        state.serviceTotal = 0
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    // 查询消费者
    findConsumer({
      state,
      commit
    }, data) {
      const vm = data[0]
      const values = data[1]
      let params = Object.assign({}, JSON.parse(JSON.stringify(values)))
      Object.keys(params).forEach(key => {
        if (params[key] === '' || params[key] === undefined || params[key] === null) {
          delete params[key]
        }
      })
      vm.$axios({
        method: 'post',
        url: Api.consumerList,
        data: params
      }).then(res => {
        state.consumerTotal = res.data ? res.data.length : 0
        const stateData = {
          data: res.data,
          name: 'consumerList'
        }
        commit('paginationChange', stateData)
        vm.loading = false
      }).catch(error => {
        vm.loading = false
        state.consumerList = []
        vm.currentPage = 1
        state.consumerTotal = 0
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    // 启用/禁用消费者
    consumerStatus({
      state
    }, data) {
      const vm = data[0]
      const params = data[1]
      let url = data[2] === 'enable' ? Api.consumerEnable : Api.consumerDisable
      vm.$axios({
        method: 'post',
        url,
        data: params
      }).then(res => {
        let news = data[2] === 'enable' ? '启用成功' : '禁用成功'
        vm.$message({
          type: 'success',
          message: news
        })
        vm.search()
      }).catch(error => {
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    // 降级
    serviceDemotion({
      state
    }, data) {
      const vm = data[0]
      const params = data[1]
      vm.$axios({
        method: 'post',
        url: Api.consumerDemotion,
        data: params
      }).then(res => {
        console.log(res)
        vm.$message({
          type: 'success',
          message: '降级成功'
        })
        vm.serviceCancel()
        vm.search()
      })
    },
    // 恢复降级
    reDemotion({
      state
    }, data) {
      const vm = data[0]
      const params = data[1]
      vm.$axios({
        method: 'post',
        url: Api.consumerReDemotion,
        data: params
      }).then(res => {
        console.log(res)
        vm.$message({
          type: 'success',
          message: '降级恢复成功'
        })
        vm.search()
      })
    }
  },
  mutations: {
    // 查询
    serviceFindTo(state, datas) {
      let vm = datas[0]
      let value = datas[1]
      vm.$axios({
        method: 'post',
        url: Api.serviceList,
        data: value
      }).then(res => {
        console.log(res)
        // 渲染state.serviceList的数据，更新表格
        state.serviceList = res.data
        vm.$message({
          message: '查询成功',
          type: 'success'
        })
      }).catch(error => {
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    paginationChange(state, content = {}) {
      state[content.name] = []
      if (content.data.length > 0) {
        let num = Math.ceil(content.data.length / 10)
        let newArray = []
        for (let i = 0; i < num; i++) {
          let a = i * 10 + 0
          let b = i * 10 + 10
          newArray.push(content.data.slice(a, b))
        }
        state[content.name] = newArray
      }
    }
  }
}



