<template>
  <div>
    <el-table
      :data="data"
      stripe
      style="width: 100%"
      :row-style="showTr">
      <el-table-column v-for="(column, index) in columns" :key="column.dataIndex"
                       :label="column.text" :width="column.width" :align="column.align" show-overflow-tooltip>
        <template slot-scope="scope">
          <template v-if="spaceIconShow(index)">
            <span v-for="(levelIndex) in scope.row._level" :key="levelIndex"
                  class="ms-tree-space"></span>
          </template>
          <span v-if="toggleIconShow(index,scope.row)"
                @click="toggleDown(scope.$index)">
            <i v-if="!scope.row._expanded" class="fa fa-chevron-right" aria-hidden="true"></i>
            <i v-if="scope.row._expanded" class="fa fa-chevron-down" aria-hidden="true"></i>
            </span>
          <span v-if="column.type==='icon'" :style="column.style">
              <i :class="scope.row.attributes[column.dataIndex]"></i>
            </span>
          <span v-else-if="column.type==='status'">
            <el-tag
              :type="scope.row.attributes[column.dataIndex] === 0 ? 'success' : 'danger'"
              disable-transitions>{{ scope.row.attributes[column.dataIndex]| userStatus }}
              </el-tag>
          </span>
          <div v-else-if="column.type==='type'" class="typeStyle" :style="{backgroundColor:scope.row.backColor}">
            {{ typeName(scope.row,scope.row.attributes[column.dataIndex])}}
          </div>
          <span v-else :style="column.style">
            {{scope.row.attributes[column.dataIndex]}}
          </span>
        </template>
      </el-table-column>
      <el-table-column v-if="treeHandel!=={}" :label="treeHandel.text" :width="treeHandel.width" align="center">
        <template slot-scope="scope">
          <slot :row="scope.row"></slot>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  export default {
    props: {
      // 这是相应的字段展示
      columns: {
        type: Array,
        default: function () {
          return []
        }
      },
      // 这是数据源
      dataSource: {
        type: Array,
        default: function () {
          return []
        }
      },
      // 这个是是否展示操作列
      treeHandel: {
        type: Object,
        default: function () {
          return {}
        }
      },
      expandPlace: {
        type: Number,
        default: function () {
          return 0
        }
      },
      // 是否默认展开所有树
      defaultExpandAll: {
        type: Boolean,
        default: function () {
          return false
        }
      }
    },
    data() {
      return {}
    },
    computed: {
      // 格式化数据源
      data: function () {
        let vm = this
        let data = vm.treeToArray(vm.dataSource, null, null, vm.defaultExpandAll)
        return data
      }
    },
    methods: {
      // 显示行
      showTr: function (data) {
        let show = (data.row._parent ? (data.row._parent._expanded && data.row._parent._show) : true)
        data.row._show = show
        return show ? '' : 'display:none;'
      },
      // 显示层级关系的空格和图标
      spaceIconShow(index) {
        if (index === this.expandPlace) {
          return true
        }
        return false
      },
      // 展开下级
      toggleDown: function (trIndex) {
        let vm = this
        let record = vm.data[trIndex]
        record._expanded = !record._expanded
      },
      // 点击展开和关闭的时候，图标的切换
      toggleIconShow(index, record) {
        if (index === this.expandPlace && record.children && record.children.length > 0) {
          return true
        }
        return false
      },
      typeName(row, type) {
        let text = ''
        const vm = this
        switch (type) {
          case 0:
            text = '目录'
            vm.$set(row, 'backColor', '#67C23A')
            break
          case 1:
            text = '菜单'
            vm.$set(row, 'backColor', '#409EFF')
            break
          case 2:
            text = '按钮'
            vm.$set(row, 'backColor', '#E6A23C')
            break
          default:
        }
        return text
      }
    }
  }
</script>
<style type="text/scss" lang="scss" scoped>
  .ms-tree-space {
    position: relative;
    top: 1px;
    display: inline-block;
    font-style: normal;
    font-weight: 400;
    line-height: 1;
    width: 18px;
    height: 14px;
  }

  .ms-tree-space::before {
    content: ""
  }

  .typeStyle {
    display: inline-block;
    padding: 1px 8px;
    color: #fff;
    border-radius: 3px;
  }

  [class^="fa fa-"] {
    font-size: 16px;
  }
</style>



