import moment from 'moment'

export default {
  pwdFormat(password) {
    const firstStr = password.charAt(0)
    const lastStr = password.charAt(password.length - 1)
    return firstStr + '****' + lastStr
  },
  dateSlice(dateTime) {
    return dateTime.substring(0, 10)
  },
  operationType(operation) {
    let res = ''
    switch (operation) {
      case 0:
        res = '普通发布'
        break
      case 1:
        res = '回滚'
        break
      case 2:
        res = '灰度发布'
        break
      case 3:
        res = '更新灰度规则'
        break
      case 4:
        res = '灰度全量发布'
        break
      case 5:
        res = '灰度发布(主版本发布)'
        break
      case 6:
        res = '灰度发布(主版本回滚)'
        break
      case 7:
        res = '放弃灰度'
        break
      default:
        res = '删除灰度(全量发布)'
    }
    return res
  },
  changeType(type) {
    let changeType = ''
    switch (type) {
      case 'ADDED':
        changeType = '新增'
        break
      case 'MODIFIED':
        changeType = '回滚'
        break
      case 'DELETED':
        changeType = '灰度发布'
        break
      default:
    }
    return changeType
  },
  formatDateTime(time) {
    return moment(time).format('YYYY-MM-DD HH:mm:ss')
  },
  userStatus(status) {
    return status === 0 ? '正常' : '禁用'
  },
  serStatus(status) {
    return status === 'true' ? '已启用' : '已禁用'
  },
  mockStatus(data) {
    let text = ''
    let _data = parseInt(data)
    switch (_data) {
      case 0:
        text = '未降级'
        break
      case 1:
        text = '容错'
        break
      case 2:
        text = '屏蔽'
        break
      default:
    }
    return text
  },
  loadBalance(data) {
    let res = ''
    switch (data) {
      case 'random':
        res = '随机'
        break
      case 'roundrobin':
        res = '轮询'
        break
      case 'leastactive':
        res = '最少并发'
        break
      default:
        res = '随机'
    }
    return res
  },
  // 主机内存单位
  hostMemory(data) {
    if (data) {
      return data + 'G'
    } else {
      return ''
    }
  },
  // 软件包类型映射
  fileTypeMap(data) {
    return data === 'FULL' ? '全量' : '增量'
  },
  // 应用状态text
  filterAppStatus(value) {
    let box = new Map([
      ['UNINSTALL', '未部署'], // 删除 修改
      ['INSTALL', '已部署'],
      ['STOPPED', '已停止'], // 启动、卸载
      ['STARTING', '启动中'],
      ['RUNNING', '运行中'], // 停止、卸载
      ['STOPPING', '停止中'],
      ['EXITED', '意外退出'],
      ['FATAL', '无法启动'],
      ['UNKNOWN', '未知'],
      ['U_CREATED', '新建'],
      ['U_SUCCEED', '成功'],
      ['U_FAILURE', '失败'],
      ['U_SEC_FAILURE', '失败'],
      ['U_COMPLETED', '完成']
    ])
    return box.get(value) ? box.get(value) : ''
  },
  // 应用状态color
  filterAppStatusColor(value) {
    let box = new Map([
      ['UNINSTALL', '#000000'],
      ['INSTALL', '#909399'],
      ['STOPPED', '#909399'],
      ['STARTING', '#67c23a'],
      ['RUNNING', '#67c23a'],
      ['STOPPING', '#909399'],
      ['EXITED', 'red'],
      ['FATAL', 'red'],
      ['UNKNOWN', 'red'],
      ['U_CREATED', '#000000'],
      ['U_SUCCEED', '#67c23a'],
      ['U_FAILURE', 'red'],
      ['U_SEC_FAILURE', 'red'],
      ['U_COMPLETED', '#67c23a']
    ])
    return {'color': box.get(value)}
  },
  instType(type) {
    let text = ''
    switch (parseInt(type)) {
      case 1:
        text = '主'
        break
      case 2:
        text = '从'
        break
      case 3:
        text = '哨兵'
        break
      default:
    }
    return text
  },
  instTypeStr(type) {
    let text = ''
    switch (type) {
      case '1':
        text = '(主)'
        break
      case '2':
        text = '(从)'
        break
      default:
        text = ''
    }
    return text
  },
  timeFilter(value, that) { // 时间转化
    let va = that.$moment(Math.trunc(value)).format('YYYY-MM-DD HH:mm:ss')
    return va
  },
  filterCommonMoType(valueData, moTypeData, type) {
    if (moTypeData && moTypeData.length > 0 && Array.isArray(moTypeData)) {
      let typeName = moTypeData.filter(item => item[type] === valueData)
      if (typeName.length > 0) {
        return typeName[0].name ? typeName[0].name : valueData
      } else {
        return valueData
      }
    } else {
      return valueData
    }
  },
  batchEnvCenter(value) {
    return value === 'A' ? '上丁中心' : '水土中心'
  },
  filterEmpty(value) {
    let box = new Map([
      ['null', '空'],
      ['undifined', '空'],
      ['false', '空']
    ])
    return box.get(value) || value
  }
}



