import axios from '../../axios/index'
import Api from '../../constants/Api.js'

export default {
  namespaced: true,
  state: {
    loading: false,
    tableLoading: false,
    dictInfo: {
      data: [],
      total: 0
    },
    typeList: [],
    dictTypeData: [],
    onlineOption: []
  },
  mutations: {
    loadingChange(state, data = false) {
      state.loading = data
    },
    tableLoadingChange(state, data = false) {
      state.tableLoading = data
    }
  },
  actions: {
    getDictData({state, commit}, params) {
      commit('tableLoadingChange', true)
      axios({
        method: 'get',
        url: Api.dictList,
        params
      }).then(res => {
        commit('tableLoadingChange', false)
        state.dictInfo.data = res.data
        state.dictInfo.total = res.totalCount
      })
    },
    getDictTypes({state}, params) {
      axios({
        method: 'get',
        url: Api.dictTypeList
      }).then(res => {
        state.typeList = res.data
      })
    },
    // 添加、修改
    tableAction({state, commit}, data) {
      commit('loadingChange', true)
      axios({
        method: 'post',
        url: data.url,
        data: data.data
      }).then(res => {
        commit('loadingChange', false)
        data.callback && (typeof data.callback === 'function') && data.callback()
      }).catch(err => {
        commit('loadingChange', false)
        data.callbackError && (typeof data.callbackError === 'function') && data.callbackError(err)
      })
    },
    deleteDict({state, commit}, data) {
      let url = Api.dictRemove.replace('{id}', data)
      return axios({
        method: 'delete',
        url
      }).then(res => res).catch(err => err)
    },
    // 根据type获取字典
    getTypeDict({state}, data) {
      axios({
        method: 'get',
        url: Api.dictListType.replace('{type}', data)
      }).then(res => {
        state.dictTypeData = res.data
      })
    },
    // 广州银行 日志索引参数
    getOnlineLogIndex({state}, data) {
      axios({
        method: 'get',
        url: Api.dictListType.replace('{type}', data)
      }).then(res => {
        state.onlineOption = res.data
      })
    }
  }
}



