import Vue from 'vue'
import { DEVGROUP } from '../constants/constant'
export default {
  commonFunc: {
    /*
    * element时间控件默认获取的UTC时间转换
    * return 中国标准时间 */
    elementDateFormat(time) {
      let formatTime = new Date(time)
      return new Date(formatTime.setHours(formatTime.getHours() + 8))
    },
    /**
     * 获取文件后缀名
     * @param {String} name 文件名
     * @return {String} 文件后缀名，例如：.zip
     */
    getSuffixName(name) {
      if (name && name.lastIndexOf('.') !== -1) {
        let lastIndex = name.lastIndexOf('.')
        let nameLen = name.length
        return name.substring(lastIndex, nameLen)
      }
    },
    deepCopy(o, c = {}) {
      for (let i in o) {
        if (typeof o[i] === 'object') {
          c[i] = (o[i].constructor === Array) ? [] : {}
          this.deepCopy(o[i], c[i])
        } else {
          c[i] = o[i]
        }
      }
      return c
    },
    arrCopy(obj) {
      var result = Array.isArray(obj) ? [] : {}
      for (var key in obj) {
        if (obj.hasOwnProperty(key)) {
          if (typeof obj[key] === 'object') {
            result[key] = this.arrCopy(obj[key])
          } else {
            result[key] = obj[key]
          }
        }
      }
      return result
    },
    validateIp(ip) {
      const checkIp = /^(25[0-5]|2[0-4]\d|[0-1]?\d?\d)(\.(25[0-5]|2[0-4]\d|[0-1]?\d?\d)){3}$/
      return checkIp.test(ip)
    },
    isDev() {
      return DEVGROUP.includes(location.hostname)
    },
    funDownload(content, filename) {
      // 创建隐藏的可下载链接
      let eleLink = document.createElement('a')
      eleLink.download = filename
      eleLink.style.display = 'none'
      // 字符内容转变成blob地址
      let blob = new Blob([content])
      eleLink.href = URL.createObjectURL(blob)
      // 触发点击
      document.body.appendChild(eleLink)
      eleLink.click()
      // 然后移除
      document.body.removeChild(eleLink)
    }
  },
  /*
   * qiaojie
   * return 中国标准时间 */
  conmmonMethods: {
    /*
    * 获得当前时间
    * return 中国标准时间 */
    getNowFormatDate() {
      var date = new Date()
      var seperator1 = '/'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate
      return currentdate
    },
    /*
    * 清空对象
    * return 中国标准时间 */
    make_clear(data) {
      if (data) {
        for (let item in data) {
          data[item] = ''
        }
      }
    },
    /* qiaojie
    * element - uiMessageBox 弹框
    * return 链式调用 */
    makePopBox(vm, words) {
      return new Promise(function (resolve, reject) {
        vm.$confirm('是否' + words, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          resolve()
        }).catch(() => {
          vm.$message({
            type: 'info',
            message: '已取消该操作'
          })
        })
      })
    },
    /* qiaojie
    * 时间
    * return 时间差 */
    makeDate({
      startTs,
      endTs
    }) {
      var dateOne = new Date(startTs)
      var dateTwo = new Date(endTs)
      let ck = dateTwo.getTime() - dateOne.getTime()
      return ck
    },
    /* qiaojie
     * 分页器
     * return 时间差 */
    arrayHandle(data) {
      if (Array.isArray(data)) {
        let num = Math.ceil(data.length / 10)
        let newArray = []
        for (let i = 0; i < num; i++) {
          let a = i * 10 + 0
          let b = i * 10 + 10
          newArray.push(data.slice(a, b))
        }
        return newArray
      } else {
        return false
      }
    }
  },
  // 时间格式化处理
  dateFtt(fmt, date) {
    const o = {
      'M+': date.getMonth() + 1, // 月份
      'd+': date.getDate(), // 日
      'h+': date.getHours(), // 小时
      'm+': date.getMinutes(), // 分
      's+': date.getSeconds(), // 秒
      'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
      'S': date.getMilliseconds() // 毫秒
    }
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (let k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
      }
    }
    return fmt
  },
  treeToArray(data, parent, level, expandedAll) {
    const vm = this
    let tmp = []
    Array.from(data).forEach(function (record) {
      if (record._expanded === undefined) {
        // eslint-disable-next-line
        Vue.set(record, '_expanded', expandedAll)
      }
      if (parent) {
        // eslint-disable-next-line
        Vue.set(record, '_parent', parent)
      }
      let _level = 0
      if (level !== undefined && level !== null) {
        _level = level + 1
      }
      // eslint-disable-next-line
      Vue.set(record, '_level', _level)
      tmp.push(record)
      if (record.children && record.children.length > 0) {
        let children = vm.treeToArray(record.children, record, _level, expandedAll)
        tmp = tmp.concat(children)
      }
    })
    return tmp
  },
  arrayIdList(data, id) {
    let arry = []
    data.forEach(res => {
      arry.push(res[id])
    })
    return arry
  },
  regExpPhone(phone) {
    let phoneReg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/
    return phoneReg.test(phone)
  },
  regExpEmail(email) {
    let emailReg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/
    return emailReg.test(email)
  }
}



