export default {
  namespaced: true,
  state: {
    historyList: [],
    hisChanges: []
  },
  mutations: {},
  actions: {
    // 获取发布历史
    getReleaseHis({state}, that) {
      const params = that.hisParams
      const pages = {
        page: that.currentPage,
        size: that.pageSize
      }
      const url = '/apollo-portal/apps/' + params.appId + /envs/ + params.env + /clusters/ + params.clusterName +
        '/namespaces/' + params.namespaceName + '/releases/histories'
      that.$axios({
        method: 'get',
        url: url,
        params: pages
      }).then(res => {
        if (pages.page > 0) {
          const _page = ++pages.page
          if (_page < res.totalPages) {
            state.historyList = that.historyList.concat(res.data)
            that.addMoreFlag = true
          } else if (_page === res.totalPages) {
            state.historyList = that.historyList.concat(res.data)
            that.addMoreFlag = false
          } else {
            that.addMoreFlag = false
          }
        } else {
          if (res.totalPages === 1) {
            state.historyList = res.data
            that.releaseTitle = res.data[0].releaseTitle
            that.releaseTime = res.data[0].releaseTime
            that.rules = res.data[0].operationContext.rules
            that.handleConfig(0, res.data[0])
            that.addMoreFlag = false
          } else if
          (res.totalPages > 1) {
            state.historyList = res.data
            that.releaseTitle = res.data[0].releaseTitle
            that.releaseTime = res.data[0].releaseTime
            that.rules = res.data[0].operationContext.rules
            that.handleConfig(0, res.data[0])
            that.addMoreFlag = true
          } else {
            that.addMoreFlag = false
          }
        }
      }).catch(err => {
        console.log(err)
      })
    },
    // 对比两次配置版本
    getCompared({state}, that) {
      const params = that.compareParams
      const url = '/apollo-portal/envs/' + that.env + '/releases/compare'
      that.$axios({
        method: 'get',
        url: url,
        params: params
      }).then(res => {
        state.hisChanges = res.data.changes
      }).catch(err => {
        console.log(err)
      })
    }
  }
}



