<template>
  <div class="login-main">
    <div class="login-box">
      <img src="../assets/images/login-box.png">
      <div class="login-right">
        <div class="login-form">
          <div class="short-line"></div>
          <h3 class="login-title">欢迎登录</h3>
          <div class="login-input mb-20">
            <i class="iconfont icon-mima"></i>
            <input type="text" name="username" v-model="username" placeholder="输入账户">
          </div>
          <div class="login-input">
            <i class="iconfont icon-zhanghu"></i>
            <input type="password" @keyup.13="authLogin" name="password" v-model="password" placeholder="输入密码">
          </div>
          <el-button :loading="loading" class="login-btn" type="primary" @click="authLogin">{{authText}}</el-button>
        </div>
        <p class="disclaimer">
          Copyright©神州数码融信软件有限公司
        </p>
      </div>
    </div>
  </div>
</template>

<script>
// import '../../static/Particleground'
import { mapState, mapActions } from 'vuex'

export default {
  data() {
    return {
      username: '',
      password: '',
      authText: '登 录',
      loading: false
    }
  },
  computed: {
    ...mapState({
      token: state => state.app.token
    })
  },
  methods: {
    ...mapActions(['getToken', 'loginAuth']),
    authLogin: async function () {
      const vm = this
      if (!vm.username) {
        vm.$message({
          type: 'error',
          message: '请输入用户名'
        })
        return
      }
      if (!vm.password) {
        vm.$message({
          type: 'error',
          message: '请输入密码'
        })
        return
      }
      const params = {
        username: vm.username,
        password: vm.password
      }
      vm.loading = true
      vm.authText = '登录中...'
      const response = await vm.getToken(params)
      vm.loading = false
      vm.authText = '登 录'
      if (response && response.access_token) {
        vm.$message.success('登录成功！')
        localStorage.setItem('userName', vm.username)
        if (vm.$route.query.redirect) {
          let redirect = vm.$route.query.redirect
          vm.$router.push(redirect)
        } else {
          vm.$router.push('/')
        }
      } else {
        vm.$message({
          type: 'error',
          message: response.data && (response.data.error_description || response.data.message)
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/common";
.login-main {
  width: 100%;
  height: 100%;
  background: url("../assets/images/login-bg.jpeg") 0 0 no-repeat;
  background-size: cover;
  .login-box {
    font-size: 14px;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 920px;
    height: 480px;
    transform: translate(-50%, -50%);
    box-shadow: 20px 20px 35px rgba(8, 19, 13, .75);
    .login-logo {
      position: absolute;
      left: 30px;
      top: 30px;
      width: 100%;
      span {
        font-size: 24px;
        color: #fff;
        font-weight: normal;
        margin-top: 20px;
      }
    }
    .login-right {
      width: 30.43%;
      position: absolute;
      top: 60px;
      right: 7.63%;
      padding: 0;
      .login-form {
        .short-line {
          width: 30px;
          border-bottom: 3px solid $blue;
        }
        .login-title {
          font-size: 20px;
          color: $blue;
          margin-top: 14px;
          letter-spacing: 2px;
          margin-bottom: 82px;
        }
      }
    }
    .login-input {
      position: relative;
      input {
        width: 100%;
        height: 35px;
        color: $gray;
        line-height: 35px;
        border: 1px solid #ccc;
        border-radius: 4px;
        outline: none;
        padding-left: 35px;
        &::-webkit-input-placeholder {
          /* WebKit, Blink, Edge */
          color: #ccc;
        }
      }
      .iconfont {
        position: absolute;
        left: 10px;
        top: 10px;
        font-size: 14px;
        color: #888e9e;
      }
    }
    .login-btn {
      margin-top: 50px;
      width: 100%;
      background: $blue;
      color: #fff;
      border: 1px solid $blue;
      height: 35px;
      line-height: 35px;
      padding: 0;
      font-size: 14px;
    }
    .disclaimer {
      text-align: center;
      font-size: 12px;
      color: #656565;
      margin-top: 82px;
    }
  }
}
</style>



