<template>
  <div class="dc-ip-input">
    <span v-for="(item, index) in ipInputArr" :key="index">
      <input maxlength="3" class="ip" type="number" v-model="item.value" @change="changeValue(index)"
             @keyup="ipKey(index, item.value)" :ref="'ipInput' + index">
      <span class="circle" v-if="index !== ipInputArr.length - 1">·</span>
    </span>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        ipInputArr: [{value: ''}, {value: ''}, {value: ''}, {value: ''}],
        ipStr: ''
      }
    },
    props: {
      value: String
    },
    watch: {
      'value'(newVal, oldVal) {
        if (!newVal) {
          this.ipInputArr.forEach(val => {
            val.value = ''
          })
          return
        }
        if (newVal !== oldVal) {
          this.setValue()
        }
      }
    },
    mounted() {
      this.setValue()
    },
    methods: {
      ipKey(index, val) {
        const vm = this
        if (val > 255) {
          vm.ipInputArr[index].value = 255
        } else {
          vm.ipInputArr[index].value = val
        }
        if (val.length >= 3 && index < 3) {
          vm.$refs['ipInput' + (index + 1)][0].focus()
        }
      },
      setValue() {
        if (this.value) {
          const valueArr = this.value.split('.')
          valueArr.forEach((val, i) => {
            this.ipInputArr[i].value = val
          })
        }
      },
      changeValue(index) {
        const vm = this
        vm.ipStr = ''
        vm.ipInputArr.forEach(item => {
          vm.ipStr += item.value + '.'
        })
        vm.ipStr = vm.ipStr.substr(0, vm.ipStr.length - 1)
        vm.$emit('getIpValue', vm.ipStr)
      }
    }
  }
</script>

<style scoped>
  .dc-ip-input {
    width: 100%;
    font-size: 0;
  }
  .dc-ip-input input::-webkit-outer-spin-button{
    -webkit-appearance: none !important;
  }
  .dc-ip-input input::-webkit-inner-spin-button {
    -webkit-appearance: none !important;
  }
  .dc-ip-input input[type="number"] {
    -moz-appearance: textfield;
  }
  .dc-ip-input .ip {
    -webkit-appearance: none;
    background-color: #fff;
    height: 40px;
    line-height: 40px;
    display: inline-block;
    padding: 0 10px;
    width: 22.7%;
    border: 1px solid #dcdfe6;
    border-radius: 4px;
    color: #606266;
    font-size: 14px;
    text-align: center;
    -webkit-transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
    transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
  }
  .dc-ip-input .circle {
    font-size: 14px;
    margin: 3px;
  }
  .dc-ip-input .ip:focus {
    outline: 0;
    border-color: #409EFF;
  }
</style>



