import axios from 'axios'
import Api from '../../constants/Api'
import _ from 'lodash'

const actuatorMimeTypes = ['application/vnd.spring-boot.actuator.v2+json',
  'application/vnd.spring-boot.actuator.v1+json',
  'application/json']

export default {
  namespaced: true,
  state: {
    instanceList: [],
    currentInstance: {},
    instanceInfo: null,
    instanceHealth: {}
  },
  actions: {
    getApplication({ state }) {
      axios({
        method: 'get',
        url: Api.springApplication
      }).then(res => {
        state.instanceList = res
      })
    },
    getCurrentInstance({ state }, id) {
      let url = Api.springCurInstance.replace('{id}', id)
      axios({
        method: 'get',
        url
      }).then(res => {
        state.currentInstance = res
      })
    },
    fetchInfo({ state }, id) {
      let url = Api.springInstanceInfo.replace('{id}', id)
      axios({
        method: 'get',
        url,
        headers: { 'Accept': actuatorMimeTypes }
      }).then(res => {
        state.instanceInfo = res
      })
    },
    fetchHealth({ state }, id) {
      let url = Api.springInstanceHealth.replace('{id}', id)
      axios({
        method: 'get',
        url,
        headers: { 'Accept': actuatorMimeTypes }
      }).then(res => {
        state.instanceHealth = res
      })
    },
    fetchEnv({ state }, data) {
      let url = Api.springInstanceEnv.replace('{id}', data.id)
      url = url.replace('{name}', (data.name || ''))
      return axios.get(url, {
        headers: { 'Accept': actuatorMimeTypes }
      })
    },
    fetchMetrics({ state }, id) {
      let url = Api.springInstanceMetrics.replace('{id}', id)
      return axios.get(url, {
        headers: { 'Accept': actuatorMimeTypes }
      })
    },
    fetchMetricData({ state }, data) {
      const params = data.tags ? {
        tag: _.entries(data.tags)
          .filter(([, value]) => typeof value !== 'undefined' && value !== null)
          .map(([name, value]) => `${name}:${value}`)
          .join(',')
      } : {}
      let url = Api.springInstanceMetricsData.replace('{id}', data.id)
      url = url.replace('{metric}', data.metric)
      return axios.get(url, {
        headers: { 'Accept': actuatorMimeTypes },
        params
      })
    }
  }
}



