import Api from '../../constants/Api'

export default {
  namespaced: true,
  state: {
    logData: {
      data: '',
      totalPage: 0
    },
    backupList: [],
    operationLog: {
      data: [],
      totalPage: 0
    }
  },
  mutations: {
    /*
    * 高亮文本
    * params: keyword[string] 关键字
    * */
    highlightKey(state, keyword) {
      state.logData.data = state.logData.data.replace(new RegExp(keyword, 'g'), `<span class="highlight">${keyword}</span>`)
    }
  },
  actions: {
    // 查询联机、批量日志数据
    searchLogData({ state, commit }, that) {
      that.loading = true
      const params = that.paramsForm
      params.pageNo === 1 && (state.logData.data = '')
      params.startTime && (params.startTime = that.commonFunc.elementDateFormat(params.startTime))
      params.endTime && (params.endTime = that.commonFunc.elementDateFormat(params.endTime))
      state.logData.totalPage = 0
      that.$axios({
        method: 'post',
        url: Api.logSearch,
        data: params
      }).then(res => {
        if (params.pageNo > 1) {
          state.logData.data = state.logData.data + res.message
        } else {
          state.logData.data = res.message
        }
        state.logData.totalPage = res.pageTotal
        that.loading = false
        if (params.keyWord) {
          commit('highlightKey', params.keyWord)
        }
        if (params.channelSeqNo) {
          commit('highlightKey', params.channelSeqNo)
        }
        if (params.reference) {
          commit('highlightKey', params.reference)
        }
        if (params.userId) {
          commit('highlightKey', params.userId)
        }
        if (params.jobId) {
          commit('highlightKey', params.jobId)
        }
        if (params.jobTaskId) {
          commit('highlightKey', params.jobTaskId)
        }
        if (params.lastRunDate) {
          commit('highlightKey', params.lastRunDate)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    // 导出日志
    exportLog({ state }, that) {
      that.$axios({
        method: 'post',
        url: Api.logExport,
        data: that.paramsForm
      }).then(res => {
        if (res.indexOf('\\') !== -1) {
          res = res.replace(/\\/g, '/')
        }
        that.$axios.get(`/log-server/downloadLog?path=${res}`).then(resData => {
          that.commonFunc.funDownload(resData, 'galaxy.log')
        })
      }).catch(err => {
        console.log(err)
      })
    },
    // 查询备份
    searchLogBackup({ state }, that) {
      const params = that.paramsForm
      params.name = that.paramsForm.dateTime
      that.$axios({
        method: 'post',
        url: Api.logBackup,
        data: params
      }).then(res => {
        state.backupList = res.list
        that.totalPage = parseInt(res.pageTotal)
      }).catch(err => {
        console.log(err)
      })
    },
    // 日志备份
    backupLogData({ state }, that) {
      const params = {
        name: that.backupLog.dateTime
      }
      that.loading = true
      that.$axios({
        method: 'post',
        url: Api.logBackupData,
        data: params
      }).then(res => {
        that.loading = false
        that.backupVisible = false
        that.$message({
          type: 'success',
          message: '备份成功！'
        })
        that.searchBackup(that)
      }).catch(err => {
        that.loading = false
        that.$message({
          type: 'error',
          message: (err && err.data && err.data.message) || '备份失败'
        })
      })
    },
    // 日志备份还原
    backupLogReset({ state }, that) {
      const params = { name: that.backupName }
      that.$axios({
        method: 'post',
        url: Api.logBackupReset,
        data: params
      }).then(res => {
        that.$message({
          type: 'success',
          message: '备份还原成功！'
        })
      }).catch(err => {
        that.$message.error((err && err.data) || '备份还原失败')
      })
    },
    // 删除日志备份
    backupLogDelete({ state }, that) {
      const params = { name: that.backupName }
      return that.$axios({
        method: 'post',
        url: Api.logBackupDel,
        data: params
      }).then(res => { return res })
    },
    // 操作日志列表
    getOperationLog({ state }, that) {
      const params = that.paramsData
      that.$axios({
        type: 'get',
        url: Api.logOperation,
        params
      }).then(res => {
        state.operationLog = {
          data: res.data.content,
          totalPage: res.data.totalElements
        }
      })
    }
  }
}



