import Api from '../../constants/Api'
import axios from '@/axios'
export default {
  namespaced: true,
  state: {
    clusterInfo: {},
    clusterOption: [],
    selectIp: [],
    selectVersion: [],
    zkInfo: {},
    versionInfo: {},
    serverLog: [],
    zookeeperNodes: []
  },
  mutations: {
    changeStatus(state, data) {
      state.zkInfo.list[data.index].status = data.data
    },
    statusAll(state, data) {
      state.zkInfo.list.forEach(item => {
        data.forEach(res => {
          if (item.moId === res.moId) {
            item.status = res.status
          }
        })
      })
    },
    rolesAll(state, data) {
      state.zkInfo.list.forEach(item => {
        data.forEach(res => {
          if (item.moId === res.moId) {
            item.type = res.type
          }
        })
      })
    }
  },
  actions: {
    // 获取zk集群列表
    getClusterList({state}, data) {
      return axios({
        method: 'get',
        url: Api.zkCluster,
        params: data
      }).then(res => {
        let resData = res.data && JSON.parse(res.data)
        state.clusterInfo = {
          list: resData.data,
          total: resData.recordsTotal
        }
      })
    },
    // 添加、编辑、删除、部署、卸载、启动、停止
    operationTable({state}, data) {
      axios({
        method: 'get',
        url: data.url,
        params: data.data
      }).then(res => {
        data.callback && data.callback()
      }).catch(err => {
        data.callbackError && data.callbackError(err)
      })
    },
    // 获取集群下拉菜单数据
    getClusterOption({state}) {
      axios({
        method: 'get',
        url: Api.zkClusterOption
      }).then(res => {
        state.clusterOption = res.data && JSON.parse(res.data)
      })
    },
    // 获取ip下拉菜单
    getIpOption({state}) {
      axios({
        method: 'post',
        url: Api.hostSearchIpList
      }).then(res => {
        state.selectIp = res.data
      })
    },
    // 获取版本号下拉菜单
    getVersionOption({state}) {
      axios({
        method: 'post',
        url: Api.coreVersionNoCombox,
        data: {
          moType: 'Zookeeper'
        }
      }).then(res => {
        state.selectVersion = res.data
      })
    },
    // 获取zk实例列表
    getZkList({state, commit}, data) {
      const vm = data.vm
      vm.tableLoading = true
      axios({
        method: 'get',
        url: Api.zkList,
        params: data.params
      }).then(res => {
        let resData = res.data ? JSON.parse(res.data) : {}
        resData.data.forEach(item => {
          item.type = ''
        })
        state.zkInfo = {
          list: resData.data,
          total: resData.recordsTotal
        }
        const resId = resData.data.map(item => {
          return { moId: item.moId }
        })
        axios({
          method: 'post',
          url: Api.zkListStatus,
          data: resId
        }).then(res => {
          commit('statusAll', res.data)
        })
        axios({
          method: 'post',
          url: Api.zkListRole,
          data: resId
        }).then(res => {
          commit('rolesAll', res.data)
        })
        vm.tableLoading = false
      })
    },
    updateZkStatus({state, commit}, data) {
      axios({
        method: 'get',
        url: Api.zkListStatus,
        params: data.data
      }).then(res => {
        const resData = res && JSON.parse(res.data)
        commit('changeStatus', {data: resData.status, index: data.index})
      })
    },
    updateZkRole({state}, data) {
      axios({
        method: 'get',
        url: Api.zkListRole,
        params: data.data
      }).then(res => {
        const resData = res && JSON.parse(res.data)
        state.zkInfo.list[data.index].type = resData.type
      })
    },
    // 获取zk版本列表
    getZkVersionList({state}, data) {
      axios({
        method: 'post',
        url: Api.coreVersionFind,
        data
      }).then(res => {
        console.log(res)
        state.versionInfo = {
          list: res.data.data,
          total: res.data.recordsTotal
        }
      })
    },
    // zk版本编辑、删除
    versionOperation({state}, data) {
      axios({
        method: 'post',
        url: data.url,
        data: data.data
      }).then(res => {
        data.callback && data.callback()
      }).catch(err => {
        data.callbackError && data.callbackError(err)
      })
    },
    getZkListConfig({state}, data) {
      return axios({
        method: 'get',
        url: Api.zkListConfig,
        params: data
      })
    },
    updateZkListConf({state}, data) {
      axios({
        method: 'get',
        url: Api.zkListConfigUpdate,
        params: data.data
      }).then(res => {
        data.callback && data.callback()
      }).catch(err => {
        data.callbackError && data.callbackError(err)
      })
    },
    getZkServerLog({state}, data) {
      state.serverLog = []
      axios({
        method: 'get',
        url: Api.zkServiceLog,
        params: data
      }).then(res => {
        console.log(Object.prototype.toString.call((JSON.parse(res.data))))
        state.serverLog = res && res.data && JSON.parse(res.data)
      })
    },
    zkDownLoad({state}, that) {
      let box = that.downPath.split('/')
      let name = box[box.length - 1]
      that.$axios({
        method: 'get',
        url: Api.coreVersionDownload + '?path=' + that.downPath,
        responseType: 'blob'
      }).then(res => {
        that.commonFunc.funDownload(res, name)
      })
    },
    getZkListNode({state}, data) {
      state.zookeeperNodes = []
      axios({
        method: 'get',
        url: Api.zkListNode,
        params: data
      }).then(res => {
        console.log(JSON.parse(res.data))
        const resData = res && res.data && JSON.parse(res.data)
        state.zookeeperNodes = [resData]
      })
    }
  }
}



