import Api from '@/constants/Api.js'
export default {
  namespaced: true,
  state: {
    ipOptions: [],
    decisionArrOptions: [],
    moTypeListOptions: []
  },
  actions: {
    async coreGetData({commit, state}, {vm}) {
      commit('coreGetDataTo', {vm})
      await vm.$axios.get(Api.dictListMotype + 'AppType', {}).then((moTypeListData) => {
        state.moTypeListOptions = moTypeListData.data
      }).catch((error) => {
        this.$message({
          type: 'error',
          message: error
        })
      })
    }
  },
  mutations: {
    coreGetDataTo(state, {vm}) {
      vm.$axios.all([
          vm.$axios.post(Api.hostSearchIpList),
          vm.$axios.post(Api.coreFindGraysCombox)
        ])
        .then(vm.$axios.spread((hostData, decisionArrData) => {
          // 下拉Ip
          state.ipOptions = hostData.data.map((item) => {
            return {
              value: item.pkValue,
              label: item.pkDesc
            }
          })
          state.decisionArrOptions = decisionArrData.data.map((item) => {
            return {
              pkValue: Number(item.pkValue),
              pkDesc: item.pkDesc
            }
          })
        }))
    }
  }
}



