import axios from '../../axios'
import Api from '@/constants/Api'
import { Message } from 'element-ui'

export default {
  namespaced: true,
  state: {
    processingData: {
      list: [],
      total: 1
    },
    envOptions: [],
    batchTableData: [],
    batchTotal: 0
  },
  mutations: {
    paginationChange(state, content = {}) {
      state[content.name] = []
      if (content.data.length > 0) {
        let num = Math.ceil(content.data.length / 10)
        let newArray = []
        for (let i = 0; i < num; i++) {
          let a = i * 10 + 0
          let b = i * 10 + 10
          newArray.push(content.data.slice(a, b))
        }
        state[content.name] = newArray
      }
    }
  },
  actions: {
    // 获取批处理环境
    getProcessingEnv({state}) {
      axios({
        method: 'get',
        url: Api.processingEnvList
      }).then(res => {
        state.processingData.list = res.data
        state.processingData.total = res.data.length
      })
    },
    // 批处理环境添加
    processingAdd({state}, data) {
      axios({
        method: 'post',
        url: Api.processingEnvAdd,
        params: data.data
      }).then(res => {
        data.callback && (typeof data.callback === 'function') && data.callback()
      }).catch(err => {
        data.callbackError && (typeof data.callbackError === 'function') && data.callbackError(err)
      })
    },
    // 批处理环境删除
    processingDelete({state}, data) {
      axios({
        method: 'post',
        url: Api.processingEnvDelete,
        params: data.data
      }).then(res => {
        data.callback && (typeof data.callback === 'function') && data.callback()
      }).catch(err => {
        data.callbackError && (typeof data.callbackError === 'function') && data.callbackError(err)
      })
    },
    // 获取批处理环境select
    getBatchOption({state}) {
      axios({
        method: 'post',
        url: Api.processingEnvId
      }).then(res => {
        state.envOptions = res
      }).catch(err => {
        Message.error((err && err.data && err.data.message) || '获取批处理环境失败')
      })
    },
    // 获取批处理环境信息
    getBatchEvnInfo({state}, params) {
      return axios({
        method: 'get',
        url: Api.processingEnvInfo,
        params
      }).then(res => res).catch(err => err)
    },
    // 获取批处理TableData
    getBatchTableData({state, commit}, params) {
      axios({
        method: 'get',
        url: Api.processingGetBatch,
        params
      }).then(res => {
        if (res.data) {
          const stateData = {
            data: res.data,
            name: 'batchTableData'
          }
          state.batchTotal = res.data.length
          commit('paginationChange', stateData)
        }
      })
    },
    // 获取批处理系统日期
    getBatchDate({state}, params) {
      return axios({
        method: 'get',
        url: encodeURI(Api.processingGetDate),
        params
      }).then(res => res).catch(err => err)
    },
    // 更新批处理系统日期
    updateBatchDate({state}, params) {
      return axios({
        method: 'get',
        url: Api.processingUpdateDate,
        params
      }).then(res => res)
    }
  }
}



