<template>
  <div class="side-bar" v-loading="loading" element-loading-background="rgba(0, 0, 0, 0)">
    <el-menu :default-active="$route.path" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" background-color="#11243f" text-color="#e6e6e6" active-text-color="#0d91ff" unique-opened router>
      <div class="oms-log padding-logo" v-if="!isCollapse">
        <img src="../../assets/images/aries-group.svg" alt="">
      </div>
      <div class="oms-log padding-logo-small" v-else>
        <img src="../../assets/images/aries.svg" alt="">
      </div>
      <template v-for="item in navTree">
        <el-menu-item v-if="item.children.length === 0" :index="item.path" :key="item.path">
          <i :class="item.icon"></i>
          <span slot="title">{{item.text}}</span>
        </el-menu-item>
        <el-submenu v-else :index="item.path" :key="item.text">
          <template slot="title">
            <i :class="item.icon"></i>
            <span slot="title">{{item.text}}</span>
          </template>
          <template v-for="secondItem in item.children">
            <el-menu-item v-if="secondItem.children.length === 0" :index="secondItem.path" :key="secondItem.text">
              <span slot="title">{{secondItem.text}}</span>
            </el-menu-item>
            <el-submenu v-else :index="secondItem.path" :key="secondItem.text">
              <span slot="title">{{secondItem.text}}</span>
              <el-menu-item v-for="thirdItem in secondItem.children" :index="thirdItem.path" :key="thirdItem.text">
                {{thirdItem.text}}
              </el-menu-item>
            </el-submenu>
          </template>
        </el-submenu>
      </template>
    </el-menu>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  data() {
    return {
      currentRoutePath: 'cluster',
      loading: false
    }
  },
  computed: {
    ...mapState({
      isCollapse: state => state.app.isCollapse,
      navTree: state => state.app.navTree
    })
  },
  mounted() {
    const vm = this
    vm.loading = true
    vm.getNavTree(vm)
  },
  methods: {
    ...mapActions(['getNavTree']),
    handleOpen(key, keyPath) {
      // console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      // console.log(key, keyPath)
    }
  }
}
</script>

<style lang="scss">
.side-bar {
  float: left;
  height: 100%;
  overflow-y: scroll;
  .el-menu {
    border-right: none;
  }

  .el-menu--collapse {
    height: 100%;
  }

  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 240px;
    height: 100%;
  }

  .el-submenu__title {
    height: 46px;
    line-height: 46px;
  }

  .el-menu-item,
  .el-submenu {
    .el-submenu {
      padding-left: 15px;
    }
    .iconfont, .fa {
      margin-right: 15px;
      text-align: left;
      font-size: 18px;
      vertical-align: middle;
      color: #e6e6e6;
    }
    .fa {
      margin-right: 9px;
    }
    .el-menu-item.is-active {
      background: #163052 !important;
    }
    .el-menu-item {
      height: 40px;
      line-height: 40px;
      margin: 10px 0;
      padding: 0 45px 0 60px !important;
    }
  }

  [class^="fa fa-"] {
    vertical-align: middle;
    font-size: 18px;
    margin-right: 5px;
    width: 24px;
    text-align: center;
  }
  .oms-log {
    font-size: 24px;
    text-align: center;
    line-height: 100px;
    color: #fff;
    border-bottom: 1px solid #415369;
    &.padding-logo {
      padding: 0 20px;
    }
    &.padding-logo-small {
      padding: 0 10px;
    }
  }
}
</style>



