import Api from '@/constants/Api.js'
export default {
  namespaced: true,
  state: {
    // 策略列表
    strategyInfo: [],
    // 对象类型
    objectType: [],
    // 级别
    LevelLogData: [],
    // 表达式函数
    strategyFunc: [
      {
        pkDesc: 'count',
        pkValue: 'count'
      },
      {
        pkDesc: 'max',
        pkValue: 'max'
      },
      {
        pkDesc: 'min',
        pkValue: 'min'
      },
      {
        pkDesc: 'sum',
        pkValue: 'sum'
      },
      {
        pkDesc: 'avg',
        pkValue: 'avg'
      }
    ],
    // 关系符
    relation: [
      {
        lable: '大于',
        value: '>'
      },
      {
        lable: '小于',
        value: '<'
      }
    ]
  },
  actions: {
    primaryAlarm({commit}, data) {
      commit('primaryAlarmTo', data)
      commit('getAlarmLevelLogTo', data)
    },
    // 查询
    serachAlarm({
        commit
      }, data) {
      commit('serachAlarmTo', data)
    },
    // 添加
    addAlarm({
      commit
    }, data) {
      commit('addAlarmTo', data)
    },
    // 修改
    updateAlarm({
      commit
    }, data) {
      commit('updateAlarmTo', data)
    },
    // 删除
    deleteAlarm({
      commit
    }, data) {
      commit('deleteAlarmTo', data)
    }
  },
  mutations: {
    primaryAlarmTo(state, {vm}) {
      vm.$axios.get(Api.dictListMotype + 'AlarmType', {}).then((typeData) => {
        state.objectType = typeData.data
      }).catch((error) => {
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    getAlarmLevelLogTo(state, {vm}) {
      vm.$axios.get(Api.alarmLevelLog).then((res) => {
        state.LevelLogData = res.data
      }).catch((error) => {
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    serachAlarmTo(state, {vm}) {
      let query = Object.assign({
        page: vm.currentPage,
        per_page: 10
      }, vm.strategyForm)
      vm.$axios.get(Api.alarmStragegyTotal, {
          params: query
      }).then((res) => {
        let data = JSON.parse(JSON.stringify(res.data))
        Object.keys(data).forEach((key) => {
          if (data[key].judge_function_param) {
            data[key].judge_function_name = data[key].judge_function_param.function_name
            data[key].judge_function_alarm_times = data[key].judge_function_param.alarm_times
            data[key].judge_function_operators = data[key].judge_function_param.operators
            data[key].judge_function_threshold = data[key].judge_function_param.threshold
            data[key].judge_function_interval = data[key].judge_function_param.interval
          }
        })
        vm.total = res.count
        state.strategyInfo = data
        vm.loading = false
      }).catch((error) => {
        vm.loading = false
        state.strategyInfo = []
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    addAlarmTo(state, {
        vm, query
      }) {
        vm.$axios({
          method: 'post',
          url: Api.alarmStragegyAdd,
          data: query
        }).then(res => {
          vm.$message({
            type: 'success',
            message: '添加成功！'
          })
          vm.search()
        }).catch(error => {
          vm.loading = false
          vm.$message.error(error.data.message || error.message || error)
        })
    },
    updateAlarmTo(state, {vm, query}) {
      vm.$axios({
        method: 'post',
        url: Api.alarmStragegyUpdate,
        data: query
      }).then(res => {
        if (res.code === '0000') {
          vm.$message({
            type: 'success',
            message: '操作成功！'
          })
          vm.search()
        }
      }).catch((error) => {
        vm.loading = false
        vm.search()
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    deleteAlarmTo(state, {
      vm,
      id
    }) {
      vm.$axios.delete(Api.alarmStragegyDelete + id).then((res) => {
        if (res.code === '0000') {
          vm.search()
        }
      }).catch(error => {
        vm.loading = false
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    formatAlarm(state) {
      state.strategyInfo.forEach(item => { item.valid = item.valid === '1' })
    }
  }
}



