// 引入接口
import Api from '../../constants/Api'
// 服务的仓库
export default {
  namespaced: true,
  state: {
    provideData: [],
    providerList: [],
    providerTotal: 0
  },
  mutations: {
    paginationChange(state, content = {}) {
      state[content.name] = []
      if (content.data.length > 0) {
        let num = Math.ceil(content.data.length / 10)
        let newArray = []
        for (let i = 0; i < num; i++) {
          let a = i * 10 + 0
          let b = i * 10 + 10
          newArray.push(content.data.slice(a, b))
        }
        state[content.name] = newArray
      }
    }
  },
  actions: {
    // 查询
    serviceFind({commit, state}, that) {
      let params = Object.assign({}, JSON.parse(JSON.stringify(that.providerForm)))
      Object.keys(params).forEach(key => {
        if (params[key] === '' || params[key] === undefined || params[key] === null) {
          delete params[key]
        }
      })
      that.$axios({
        method: 'post',
        url: Api.providerList,
        data: params
      }).then(res => {
        state.providerTotal = res.data ? res.data.length : 0
        if (res.data) {
          const stateData = {
            data: res.data,
            name: 'providerList',
            pageSize: that.pageSize,
            currentPage: that.currentPage - 1
          }
          state.provideData = res.data
          commit('paginationChange', stateData)
          that.loading = false
        }
      }).catch((error) => {
        that.loading = false
        state.providerList = []
        state.providerTotal = 0
        that.currentPage = 1
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 禁用
    providerDisable({commit}, that) {
      that.$axios({
        method: 'post',
        url: Api.providerDisable,
        data: that.handlePara
      }).then(res => {
        that.$message({
          type: 'success',
          message: '禁用成功'
        })
        that.search()
      }).catch((error) => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 启用
    providerEnable({commit}, that) {
      that.$axios({
        method: 'post',
        url: Api.providerEnable,
        data: that.handlePara
      }).then(res => {
        that.$message({
          type: 'success',
          message: '启用成功'
        })
        that.search()
      }).catch((error) => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 降级
    providerMock({commit}, that) {
      that.$axios({
        method: 'post',
        url: Api.providerMock,
        data: that.handlePara
      }).then(res => {
        that.$message({
          type: 'success',
          message: '降级成功'
        })
        that.search()
      }).catch((error) => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 恢复降级
    providerRecoverMock({commit}, that) {
      that.$axios({
        method: 'post',
        url: Api.providerRecoverMock,
        data: that.handlePara
      }).then(res => {
        that.$message({
          type: 'success',
          message: '降级恢复成功'
        })
        that.search()
      }).catch((error) => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 权重
    providerWeight({commit}, that) {
      that.$axios({
        method: 'post',
        url: Api.providerWeight,
        data: that.handlePara
      }).then(res => {
        that.$message({
          type: 'success',
          message: '权重修改成功'
        })
        that.search()
      }).catch((error) => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 负载均衡
    providerLoadBal({commit}, that) {
      that.$axios({
        method: 'post',
        url: Api.providerLoadBal,
        data: that.handlePara
      }).then(res => {
        that.$message({
          type: 'success',
          message: '负载均衡修改成功'
        })
        that.search()
      }).catch((error) => {
        that.$message.error(error.data.message || error.message || error)
      })
    }
  }
}



