// 引入接口
import Api from '../../constants/Api'

export default {
  namespaced: true,
  state: {
    // 添加分类
    classify: {
      clusterName: [], // 集群分类
      ipGroup: [], // ip分类
      claType: [], // 类型分类
      claVersion: [] // 版本分类
    },
    redisCluster: [],
    masterSlave: [],
    // redis列表数据
    redisList: [],
    redisVersion: [],
    redisCharts: [],
    redisLog: ''
  },
  mutations: {
    redisStatusAll(state, data) {
      state.redisList.forEach(item => {
        data.forEach(res => {
          if (item.moId === res.moId) {
            item.status = res.status
            item.instType = res.instType
            item.instTypeStr = res.instTypeStr
          }
        })
      })
    }
  },
  actions: {
    // redis集群查询
    findRedisCluster({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.findRedisCluster,
        params: that.clusterParam
      }).then(res => {
        let _res = JSON.parse(res.data)
        that.totalPage = _res.recordsTotal
        state.redisCluster = _res.data
        that.loading = false
      }).catch(error => {
        console.log(error)
        that.loading = false
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 集群主从列表
    findRedisMasterToSlave({state}, that) {
      state.masterSlave = []
      that.$axios({
        method: 'get',
        url: Api.findRedisMasterToSlave,
        params: {
          clusterId: that.clusterId
        }
      }).then(res => {
        let _res = JSON.parse(res.data)
        state.masterSlave = _res
        console.log(state.masterSlave)
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis实例查询
    findRedisInst({state, commit}, that) {
      that.$axios({
        method: 'get',
        url: Api.findRedisInst,
        params: that.redisIntParam
      }).then(res => {
        let _res = JSON.parse(res.data)
        that.totalPage = _res.recordsTotal
        state.redisList = _res.data
        const resId = _res.data.map(item => {
          return { moId: item.moId }
        })
        that.$axios({
          method: 'post',
          url: Api.getRedisStatus,
          data: resId
        }).then(res => {
          commit('redisStatusAll', res.data)
        })
        that.loading = false
      }).catch(error => {
        that.loading = false
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 添加redis集群
    addRedisCluster({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.addRedisCluster,
        params: that.listForm
      }).then(res => {
        that.$message.success('集群添加成功')
        that.search()
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 删除redis集群
    deleteRedisCluster({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.deleteRedisCluster,
        params: {
          id: that.listForm.id
        }
      }).then(res => {
        that.$message.success('集群删除成功')
        that.search()
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 修改redis集群
    updateRedisCluster({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.updateRedisCluster,
        params: that.listForm
      }).then(res => {
        that.$message.success('集群更新成功')
        that.search()
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis列表下拉数据(类型,集群，版本,ip)
    findRedisCombox({
        state
      }, that) {
        that.$axios.all([
            that.$axios.get(Api.findRedisInstTypeCombox),
            that.$axios.get(Api.findRedisClusterCombox),
            that.$axios.post(Api.findVersionNoCombox, {'moType': 'Redis'}),
            that.$axios.post(Api.hostSearchIpList, {'moType': 'redis'})
          ])
          .then(that.$axios.spread((instTypeData, clusterData, versionData, hostData) => {
            state.classify.claType = JSON.parse(instTypeData.data)
            state.classify.clusterName = JSON.parse(clusterData.data)
            state.classify.claVersion = versionData.data
            // 下拉Ip
            state.classify.ipGroup = hostData.data.map((item) => {
              return {
                value: item.pkValue,
                label: item.pkDesc
              }
            })
          })).catch(error => {
            that.$message.error(error.data.message || error.message || error)
          })
    },
    // redis版本查询
    findRedisVersion({state}, that) {
      let param = Object.assign({}, {
        'pageNo': that.pageNo,
        'limit': that.limit,
        'baseParam': {
          'moType': 'Redis',
          'versionNo': that.seacrchContent
        }
      })
      that.$axios({
        method: 'post',
        url: Api.coreVersionFind,
        data: JSON.stringify(param)
      }).then(res => {
        that.totalPage = res.data.recordsTotal
        state.redisVersion = res.data.data
        that.loading = false
      }).catch(error => {
        that.loading = false
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis版本更新
    redisVersionUpdate({state}, that) {
      let {id, name, remark} = that.verEditForm
      if (!name) {
        that.$message({
          type: 'error',
          message: '版本名称不能为空'
        })
        return
      }
      let param = Object.assign({}, {id, name, remark})
      that.$axios({
        method: 'post',
        url: Api.coreVersionUpdate,
        data: JSON.stringify(param)
      }).then(res => {
        that.redisVerFind()
        that.$message.success(`更新成功`)
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis版本添加
    redisVersionAdd({state}, that) {
      let {method, moType, fileType, name, versionNo, remark, path, creator, sourceFile} = that.verAddForm
      if (method === 'Local') {
        if (!name || !versionNo || !sourceFile) {
          that.$message({
            type: 'error',
            message: '带*不能为空'
          })
          return
        }
      } else {
        if (!name || !versionNo || !path) {
          that.$message({
            type: 'error',
            message: '带*不能为空'
          })
          return
        }
      }
      let _data = {moType, fileType, name, versionNo, remark, path, creator, sourceFile}
      console.log(_data.sourceFile)
      let formData = new FormData()
      Object.keys(_data).forEach((value) => {
        if (_data[value]) {
          formData.append(value, _data[value])
        }
      })
      that.$axios({
        method: 'post',
        url: Api.coreVersionAdd,
        params: {
          method: method
        },
        data: formData,
        onUploadProgress: (progressEvent) => {
          if (progressEvent.lengthComputable) {
            that.percentageNum = Math.round((progressEvent.loaded / progressEvent.total) * 100)
          }
        }
      }).then(res => {
        that.addVisible = false
        that.redisVerFind()
        that.$message.success(`添加成功`)
      }).catch(error => {
        that.percentageNum = 0
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis版本删除
    redisVersionRemove({state}, that) {
      let param = {
        'id': that.verEditForm.id
      }
      that.$axios({
        method: 'post',
        url: Api.coreVersionDelete,
        data: JSON.stringify(param)
      }).then(res => {
        that.redisVerFind()
        that.$message.success(`删除成功`)
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis版本下载
    redisVersionDownload({state}, that) {
      let box = that.downPath.split('/')
      let name = box[box.length - 1]
      that.$axios({
        method: 'get',
        url: Api.coreVersionDownload + '?path=' + that.downPath,
        responseType: 'blob'
      }).then(res => {
        that.commonFunc.funDownload(res, name)
      })
    },
    // redis实例添加
    addRedisInst({state}, that) {
      let _data = {
        status: that.redisForm.method,
        name: that.redisForm.name,
        groupName: that.redisForm.groupName,
        clusterId: that.redisForm.clusterId,
        ip: that.redisForm.ip,
        instType: that.redisForm.instType,
        versionNo: that.redisForm.versionNo,
        installPath: that.redisForm.installPath,
        installName: that.redisForm.installName
      }
      that.$axios({
        method: 'get',
        url: Api.addRedisInst,
        params: _data
      }).then(res => {
        that.$message.success('实例添加成功')
        that.search()
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis实例信息更新
    updateRedisInst({state}, that) {
      let _data = that.redisForm
      that.$axios({
        method: 'get',
        url: Api.updateRedisInst,
        params: _data
      }).then(res => {
        that.$message.success(`更新成功`)
        that.search()
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis实例删除
    deleteRedisInst({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.deleteRedisInst,
        params: {
          'redisIds': that.redisForm.moId
        }
      }).then(res => {
        that.$message.success(`删除成功`)
        that.search()
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // redis实例配置更新
    updateRedisConf({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.updateRedisConf,
        params: that.configForm
      }).then(res => {
        that.search()
        that.$message.success(`配置更新成功`)
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // Redis实例 部署
    deployRedis({state}, that) {
      that.loading = true
      that.$axios({
        method: 'get',
        url: Api.deployRedis,
        params: {
          'redisIds': that.redisForm.moId
        }
      }).then(res => {
        that.$message.success(`部署成功`)
        that.loading = false
        that.search()
      }).catch(error => {
        that.loading = false
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // Redis实例 卸载
    cleanRedis({state}, that) {
      that.loading = true
      that.$axios({
        method: 'get',
        url: Api.cleanRedis,
        params: {
          'redisIds': that.redisForm.moId
        }
      }).then(res => {
        that.$message.success(`卸载成功`)
        that.loading = false
        that.search()
      }).catch(error => {
        that.loading = false
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // Redis实例 启动
    startRedis({state}, that) {
      that.loading = true
      that.$axios({
        method: 'get',
        url: Api.startRedis,
        params: {
          'redisIds': that.redisForm.moId
        }
      }).then(res => {
        that.$message.success(`启动成功`)
        that.loading = false
        that.search()
      }).catch(error => {
        that.loading = false
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // Redis实例 停止
    stopRedis({state}, that) {
      that.loading = true
      that.$axios({
        method: 'get',
        url: Api.stopRedis,
        params: {
          'redisIds': that.redisForm.moId
        }
      }).then(res => {
        that.$message.success(`停止成功`)
        that.loading = false
        that.search()
      }).catch(error => {
        that.loading = false
        that.$message.error(error.data.message || error.message || error)
      })
    },
    getRedisLog({state}, data) {
      const that = data[0]
      state.redisLog = []
      that.loading = true
      that.$axios({
        method: 'get',
        url: Api.getRedisLog,
        params: data[1]
      }).then(res => {
        let _res = JSON.parse(res.data)
        state.redisLog = _res
        that.loading = false
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    },
    // 通过moId查询实例状态
    getRedisStatus({state}, that) {
      that.$axios({
        method: 'get',
        url: Api.getRedisStatus,
        params: {
          'moId': that.redisForm.moId
        }
      }).then(res => {
        let _res = JSON.parse(res.data)
        state.redisList[that.idx] = Object.assign(state.redisList[that.idx], _res)
      }).catch(error => {
        that.$message.error(error.data.message || error.message || error)
      })
    }
  }
}



